<template>
    <div>
        <a class="mint-cell mint-field">
            <div class="mint-cell-left"></div>
            <div class="mint-cell-wrapper">
                <div class="mint-cell-title" v-if="this.label">
                    <span class="mint-cell-text">{{this.label}}</span>
                </div>
                <div class="mint-cell-value" v-bind:class="{formatInput:this.format}">
                    <input
                        :value="this.value"
                        ref="input" 
                        v-validate="this.validate"
                        :data-vv-as="this.label" 
                        :placeholder="this.placeholder"
                        :type="this.type" 
                        v-on:input="updateValue($event.target.value)"
                        v-on:focusout="errorChange"
                        :name="this.name" 
                        :maxlength="this.maxlength"
                        class="mint-field-core" 
                        v-if="this.type !='select'"
                        />
                    <div class="select-wrap" v-if="this.type=='select'">
                        <select 
                            :value="this.value"
                            v-on:input="updateValue($event.target.value)">
                            <option 
                                v-for="(i,index) in options" 
                                :value="i.value" >
                                {{i.label}}
                            </option>
                        </select>
                        <i class="iconfont icon-xiala2"></i>
                    </div>

                    <span v-if="this.format" class="format">
                        <span class="placeholder" v-if="this.value==''">{{this.placeholder}}</span>
                        {{this.formatValue}}
                    </span>
                </div>
            </div>
            <div class="mint-cell-right"></div>
        </a>
        
        <div v-show="errors.has(this.name)" class="help-bar is-danger">{{ errors.first(this.name) }}</div>
    </div>
</template>
<script>

export default {
    props: {
        label: {
            type: String,
            default: '',
        },
        value: {
            type: String,
            default: '',
        },
        name: {
            type: String,
            default: '',
        },
        vname: {
            type: String,
            default: '',
        },
        validate: {
            type: String,
            default: '',
        },
        placeholder: {
            type: String,
            default: '',
        },
        type: {
            type: String,
            default: '',
        },
        maxlength: {
            type: String,
            default: '',
        },
        format: {
            type: String,
            default: '',
        },
        options: {
            type: Array
        }
    },
    data() {
        return {
            isRight:false
        }
    },
    computed : {
        formatValue : function(){
            if(this.format){
                switch(this.format){
                    case 'crecard':
                        var ncard ='' ;
                        this.value = this.value.replace(/\D/g,'')
                        for(var n=0;n<this.value.length;n=n+4){
                            ncard += this.value.substring(n,n+4)+" ";
                        }
                        return ncard
                    break;
                    default:
                       return this.value
                }
            }else{
                return this.value
            }
        }
    },
    methods: {
        updateValue: function(value) {
            this.$emit('input', value)
        },
        errorChange : function(){
            var res = this.value == "" ? 'null' : !this.errors.has(this.name);
            var data = `{"${this.name}":${res}}`;
            this.$eventBus.$emit('errorsChanged',data);
        }
    },
    mounted : function(){
        this.$nextTick(function(){
           this.errorChange();
        });
    }
}
</script>
<style lang="less" rel="stylesheet/less" scoped>
    .mint-cell-value{
        input{
            font-size:1.4rem;
        }
    }
    .formatInput{
        position: relative;
        input{
            position: absolute;
            opacity: 0;
            letter-spacing: 1px;
        }
        span.format{
            margin-left: 6px;
        }
    }
</style>



