<template>
    <div class="wrap">
        <mt-header title="找回密码">
            <router-link to="/b_login" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
        </mt-header>
        <div class="main">
            <Field v-model="output.cust_name" validate="required" name="username" placeholder="企业工商注册名" type="text" />
			<Field v-model="output.cust_name" validate="required|name" name="username" placeholder="营业执照号" type="text" />
            <Field v-model="output.cust_name" validate="required|name" name="username" placeholder="法人姓名" type="text" />
            <Field v-model="output.cust_name" validate="required|name" name="username" placeholder="法人证件号" type="text" />
            <Field v-model="output.cust_name" validate="required|name" name="username" placeholder="结算账户名" type="text" />
            <Field v-model="output.cust_name" validate="required|name" name="username" placeholder="结算账号" type="text" />
            <div class="btn">
                <mt-button type="primary" size="large" @click="register">确定</mt-button>
            </div>
        </div>
    </div>
</template>

<script>
import Field from  '@/components/field';
export default {
    components: {
        Field
    },
    data() {
        return {
            output : {
                cust_name : ''
            },
            verifyBtnText: '获取验证码'
        }
    },
    methods: {
        showAddress (res){
            this.output.address = res;
        },
        showAddressDetail(res){
            this.output.detail = res;
        },
        register(){

        }
    },
    created() {
        this.$eventBus.$on('getAddress',this.showAddress);
        this.$eventBus.$on('getDetail',this.showAddressDetail);
    }
}
</script>


<style lang="less" rel="stylesheet/less" scoped>
    .btn{
        margin:15px;
    }
</style>



