<template>
	<div class="wrap">
		<mt-header :title="data.title">
            <mt-button icon="back" slot="left" @click="close">返回</mt-button>
        </mt-header>
        <div class="main">
            <Contract1 :data="data" />
        </div>
    </div>
</template>
<script>
import Contract1 from './contract_1.vue'

export default {
    props : ['data'],
    components: {
        Contract1
	},
	methods: {
        close(){
            this.$emit('close')
        },
		// 解决父级窗口一起滚动的bug
        fixedPage(e){
            e.preventDefault();
        }
	}
}
</script>


