export default class StageVO {
  constructor (data) {
    data = data || {}
    this.mer_id = data.mer_id || ''
    this.mer_name = data.mer_name || ''
    this.scene_id = data.scene_id || ''
    this.prdtNo = data.prdtNo || ''
    this.mer_prdt_rate = data.mer_prdt_rate_arrays.mer_prdt_rate || []
  }
}



