<template>
    <div class="common-popup mint-msgbox">
        <div class="mint-msgbox-header">
            <div class="close-btn" @click="cancel">
                <i class="icon iconfont icon-close"></i>
            </div>
            <div class="mint-msgbox-title">{{title}}</div>
        </div>
        <div v-show="!loading && !success" class="entering">
            <div class="common-content">
                <div class="mask-field">
                    <!-- <input ref="pswInput" type="tel" class="psw-6" maxlength="6" v-model="psw"/> -->
                    <ul class="psw-6-mask">
                        <li v-for="item in 6">
                            <span v-show="psw[item-1]">*</span>
                        </li>
                    </ul>
                </div>
            </div>
            <!-- <div class="mint-msgbox-btns">
                <button class="mint-msgbox-btn mint-msgbox-cancel" @click="cancel">取消</button>
                <button class="mint-msgbox-btn mint-msgbox-confirm" @click="confirm">确定</button>
            </div> -->
            <NumberKeyboard v-on:input="numberUpdate" v-on:finish="confirm" />
        </div>
        <div class="loading" v-if="loading">
           <i class="iconfont icon-loading spin"></i>
        </div>
        <!-- <div class="success" v-if="success">
           <i class="iconfont icon-check"></i>
           <p>密码检验成功</p>
        </div> -->
    </div>
</template>

<script>
import md5 from '@/plugins/md5';
import NumberKeyboard from '@/components/numberkeyboard';
export default {
    props:['title'],
    components:{
        NumberKeyboard
    },
    data() {
        return {
            loading: false,
            success : false,
            psw : ''
        }
    },
    methods : {
        cancel : function(){
            this.$emit('close');
        },
        confirm : function(){
            //console.log(md5(this.psw))
            var self = this;
            this.loading = true;
            this.$emit('confirm',this.psw);
        },
        numberUpdate(v){
            this.psw = v;
        }
    },
    created : function(){
        var self = this;
        this.$eventBus.$on('entercardReload',function(){
            self.loading = false;
        });
    }
}
</script>

<style lang="less" rel="stylesheet/less" scoped>
    @import url('../../../styles/variable.less');
    
    .mint-msgbox-title{
        font-size: 1.8rem;
    }
    .mask-field{
        position:relative;
        margin:0 5vw;
        height: 55px;
        .psw-6-mask{
            position: absolute;
            list-style: none;
            margin:0;
            padding:0;
            width:100%;
            z-index: 1;
            font-size: 20px;
            height: 55px;
            line-height: 55px;
            display: flex;
            border:1px solid #ddd;
            overflow: hidden;
            li{
                border-right:1px solid #ddd;
                padding:0;
                flex: 1;
                color: #333;
                height: 55px;
                line-height: 65px;
                text-align: center;
                font-size: 3rem;
                &:last-child{
                    border:0;
                }
            }
        }
        .psw-6{
            z-index: 2;
            position: absolute;
            width: 100%;
            height: 55px;
            line-height: 55px;
            opacity: 0;
            text-indent: -999em;
            margin-left: -100%;
            width: 200%; 
        }
    }
    .entering{
        height: 345px;
    }
    .success,.loading{
        padding-top:60px;
        height: 285px;
    }
    .success,.loading{
        width: 100%;
        text-align: center;
    }
    .loading{
        color: @gray_2;
        i{
            font-size:14rem;
        }
    }
    .success{
        color:@green_2;
        font-size: 2rem;
        i{
            font-size:14rem;
        }
    }
   
</style>


