<template>
    <div class="verify-box">
        <Field v-model="verify_code" :label="label" validate="required|verifyvode" name="verifycode" placeholder="验证码" type="tel" maxlength='4'></Field>
        <mt-button type="primary" size="small" plain @click="verifyTel">{{verifyBtnText}}</mt-button>
    </div>
</template>

<script>
import Field from  '@/components/field';
import { MessageBox,Toast } from 'mint-ui';
import * as LFQSendSMS from '@/services/LFQSendSMS';
var regex = "^((13[0-9])|(14[5|7])|(15([0-3]|[5-9]))|(18[0,5-9]))\\d{8}$"; 

export default {
    props : ['label','value','hpNo'],
    components : {
        Field
    },
    data(){
        return {
            verify_code : '',
            verifyBtnText: '获取验证码',
			getCodeTime: 60
        }
    },
    methods: {
        verifyTel() {
            var _this = this;
			if(!this.hpNo.match(regex)){
				return Toast({
                    message: '请输入有效的手机号码',
                    position: 'center',
                    duration: 1000
                });
            }	
 
            LFQSendSMS.sendSms({
                hp_no:_this.hpNo,
                openid:_this.GLOBAL.OPENID,
                prdt_no:_this.GLOBAL.PRDTNO
            }).then(function(res) {
                Toast({
                    message: '验证码已发送',
                    position: 'center',
                    duration: 1000
                });
                var getCodeTime = 60;
                _this.verifyBtnText = `重新获取(${getCodeTime}s)`
                var setIntervarHandle = setInterval(function() {
                    getCodeTime--;
                    _this.verifyBtnText = `重新获取(${getCodeTime}s)`
                    if (getCodeTime == 0) {
                        _this.verifyBtnText = '获取验证码'
                        clearInterval(setIntervarHandle);
                        getCodeTime = 60;
                    }
                }, 1000)
            }, this);
		}
    },
    updated(){
        this.$emit('update',this.verify_code);
    }
}
</script>
<style lang="less" rel="stylesheet/less" scoped>
    .verify-box {
        position: relative;
        button{
            position: absolute;
	        top: 7px;
	        right: 15px;
        }
    }
</style>



