<template>
	<div class="wrap">
		<mt-header title="申请开通">
			<router-link to="/b_login" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
		</mt-header>
		<div class="main">
			<Field v-model="output.cust_name" validate="required" name="username" placeholder="企业工商注册名" type="text" />
			<Field v-model="output.cust_name" validate="required|name" name="username" placeholder="联系人姓名" type="text" />
			<Field v-model="output.hp_no" validate="required|telno" name="telno" placeholder="手机号码" type="tel" maxlength='11'></Field>
			<SendSms v-model="output.verify_code" :hpNo="output.hp_no" v-on:update="function(v){output.verify_code = v}" />
	
			<PickAddress :address="output.address" :detail="output.detail" />
			<div class="btn">
				<mt-button type="primary" size="large" @click="register">申请</mt-button>
			</div>
		</div>
	</div>
</template>

<script>
	import Field from '@/components/field';
	import PickAddress from '@/components/address';
	import SendSms from '@/components/sendsms';
	import { MessageBox } from 'mint-ui';
	export default {
		components: {
			Field,
			PickAddress,
			SendSms
		},
		data() {
			return {
				output: {
					cust_name: '',
					hp_no: '',
					verify_code: '',
					address: '',
					detail: ''
				},
				verifyBtnText: '获取验证码'
			}
		},
		methods: {
			showAddress(res) {
				this.output.address = res;
			},
			showAddressDetail(res) {
				this.output.detail = res;
			},
			register() {
				MessageBox({
					title: '已受理您的申请',
					message: '将由客户经理联系您，完成开通',
					showCancelButton: false,
					confirmButtonHighlight : true
				}).then(function(res){
					console.log(res)
				})
			}
		},
		created() {
			this.$eventBus.$on('getAddress', this.showAddress);
			this.$eventBus.$on('getDetail', this.showAddressDetail);
		}
	}
</script>


<style lang="less" rel="stylesheet/less" scoped>
	.btn {
		margin: 15px;
	}
</style>



