<template>
    <div class="keyboard">
        <div class="row" v-for="i in 3">
            <div @touchstart="enter(j+(3*i)-3)" v-for="j in 3">{{j+(3*i)-3}}</div>
        </div>
        <div class="row">
            <div class="empty"></div>
            <div @touchstart="enter(0)">0</div>
            <div @touchstart="back" class="empty back">
                <i class="icon iconfont icon-tuige"></i>
            </div>
        </div>
    </div>
</template>

<script>
import md5 from '@/plugins/md5';

export default {
    data() {
        return {
            enterNum : ''
        }
    },
    methods : {
        enter(num){
            this.enterNum += '' + num;
        },
        back(){
            var p = this.enterNum.split('');
            p.pop();
            this.enterNum = p.join('');
        }
    },
    watch : {
        enterNum : function(v){
            this.$emit('input',v);
            if(v.length == 6){
                setTimeout(() => {
                    this.$emit('finish');
                }, 400);
            }
            if(v.length > 6){
                this.back();
            }
        }
    }
}
</script>

<style lang="less" rel="stylesheet/less" scoped>
     .keyboard{
        list-style: none;
        text-align: center;
        width:100%;
        margin:0;
        padding:0;
        flex-wrap : wrap;
        font-size: 2.8rem;
         -webkit-tap-highlight-color:rgba(0,0,0,0.2);
        .row{
            display: flex;
            >div{
                flex:1;
                box-sizing: border-box;
                border:1px solid #ddd;
                border-top:0;
                width: 100%;
                height: 60px;
                line-height: 60px;
                &:nth-child(3n+2){
                    border-left:0;
                    border-right:0;
                }
                &.empty{
                    background:#f0f0f0;
                }
                &.back{
                    .iconfont{
                        font-size: 3rem;
                    }
                }
            }
        }
    }
</style>


