<template>
	<div class="wrap">
		<mt-header title="修改登录密码">
			<mt-button icon="back" slot="left" @click="close">返回</mt-button>
		</mt-header>
		<div class="main">
			<div class="psd-box">
				<div class="input-box border_b">
					<input type="num" placeholder="请输入原密码">
				</div>
				<div class="input-box border_b">
					<input type="num" placeholder="请输入新密码">
				</div>
				<div class="input-box border_b">
					<input type="num" placeholder="请再次输入新密码">
				</div>
			</div>
	
			<div class="button-box">
				<mt-button type="primary" size="large" v-bind:disabled="!isAllowed" @click="getRes">确定</mt-button>
			</div>
		</div>
		<mt-popup v-model="tipPopupVisible" class="pop-box">
			<div class="icon-box tc">
				<span class="icon iconfont icon-success f50 blue_1"></span>
			</div>
			<p class="tc f20 blue_1 ">修改密码成功</p>
		</mt-popup>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				isAllowed: true,
				tipPopupVisible: false
			}
		},
		methods: {
			close() {
				this.$emit('close')
			},
			// 解决父级窗口一起滚动的bug
			fixedPage(e) {
				e.preventDefault();
			},
			getRes() {
				this.tipPopupVisible = true;
			}
		}
	}
</script>

<style lang="less" scoped>
	.psd-box {
		padding: 0 10px;
		background-color: #fff;
	}
	
	.pop-box {
		width: 70%;
		padding-bottom: 20px;
	}
	
	.icon-box {
		padding: 40px 0 20px;
	}
</style>




