<template>
	<div class="wrap">
		<mt-header title="费率配置">
			<router-link to="/b_managerhome" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
		</mt-header>
		
		<div class="main">
            <div class="l-btn">
                <i class="iconfont icon-zengjia"></i> 商户费率配置
            </div>

            <div class="">
                <Field 
                    v-model="output.s1" 
                    label="分期期数" 
                    validate="required" 
                    name="username" 
                    type="select"
                    :options="s1"></Field>
                <Field 
                    v-model="output.s2" 
                    label="供应商" 
                    validate="required" 
                    name="username" 
                    type="select"
                    :options="s2"></Field>
                <Field 
                    v-model="output.s3" 
                    label="分期模式" 
                    validate="required" 
                    name="username" 
                    type="select"
                    :options="s3"></Field>
                    
                <Field v-model="output.cust_name" placeholder="商户扣率%" validate="required" name="username" type="tel"></Field>
			    <Field v-model="output.cust_name" placeholder="持卡人手续费率%" validate="required" name="username" type="tel"></Field>

                <div class="p-15">
                    <router-link to="/b_managerhome">
                        <mt-button type="primary" size="large">确定</mt-button>
                    </router-link>
                </div>

            </div>
		</div>
		
	</div>
</template>

<script>
    import Field from '@/components/field';
	export default {
		components: {
            Field
		},
		data() {
			return {
				output: {
                    cust_name : '',
                    s1 : '9',
                    s2 : '',
                    s3 : ''
                },
                s1 : [
                    {
                        value : '6',
                        label : '6期'
                    },
                    {
                        value : '9',
                        label : '9期'
                    },
                    {
                        value : '12',
                        label : '12期'
                    }
                ],
                s2 : [
                    {
                        value : '1',
                        label : '银骏科技'
                    }
                ],
                s3 : [
                    {
                        value : '1',
                        label : '持卡人全额承担'
                    },
                    {
                        value : '2',
                        label : '商户金额承担'
                    },
                    {
                        value : '3',
                        label : '自主费率'
                    }
                ]
			}
		},
		methods: {
            
		},
		created() {
	
		}
	}
</script>


<style lang="less" rel="stylesheet/less" scoped>
	@import "../../../../../styles/variable.less";
    .l-btn{
        background:@white_1;
        text-align: center;
        line-height: 3.4;
        border-bottom: 1px solid @gray_3;
        font-size: 1.8rem;
        color: @blue_1;
    }
</style>



