import Vue from 'vue'
import Validate from 'vee-validate'
import { Validator } from 'vee-validate'
const config = {
	events: 'blur'
}
const dictionary = {
	en: {
		messages: {
			alpha: () => 'Some English Message'
		}
	},
	zh: {
		// attributes: {
		//     email: '邮箱'
		// },
		messages: {
			required: function (res) {
				return `${res}不能为空`
			},
			max: function (res) {
				return `${res}长度不符合要求`
			},
			regex: function (res) {
				return `${res}格式不符合要求`
			},
			digits: function (res) {
				return `${res}长度不符合要求`
			}

		},
		custom: {
			email: {
				required: '邮箱地址不能为空',
				email: '邮箱地址不正确'
			},
			crecardno: {
				credit_card: '请输入有效的信用卡号'
			}
		}
	}
}

Validator.extend('name', {
	messages: {
		zh: field => field + '格式不符合要求'
	},
	validate: value => {
		return /[\u4E00-\u9FA5]{2,4}/.test(value)
	}
})
Validator.extend('idno', {
	messages: {
		zh: field => field + '格式不符合要求'
	},
	validate: value => {
		return /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9*]$/.test(value.replace(/  /g, ""))
	}
})
Validator.extend('cvnno', {
	messages: {
		zh: field => field + '必须是三位数字'
	},
	validate: value => {
		return /\d{3}/.test(value)
	}
})
// Validator.extend('creno', {
//   messages: {
//     zh: field => field + '格式不符合要求'
//   },
//   validate: value => {
//     return /\d{13,16}/.test(value.replace(/  /g, ""))
//   }
// })
Validator.extend('time', {
	messages: {
		zh: field => field + '不符合要求'
	},
	validate: value => {
		return /^((0[1-9])|(10|11|12))\d{2}$/.test(value)
	}
})
Validator.extend('validtime', {
	messages: {
		zh: field => field + '必须大于当前日期'
	},
	validate: value => {
		var nowDate = new Date()
		var nowMonth = nowDate.getMonth() + 1
		var nowYear = Number.parseInt((nowDate.getFullYear() + '').substring(2, 4))
		if (parseInt(value.substring(2, 4)) > nowYear) {
			return true
		} else if (parseInt(value.substring(2, 4)) == nowYear && parseInt(value.substring(0, 2)) > nowMonth) {
			return true
		} else {
			return false
		}
	}
})
Validator.extend('telno', {
	messages: {
		zh: field => field + '必须是11位号码'
	},
	validate: value => {
		return /\d{11}/.test(value)
	}
})
Validator.extend('verifyvode', {
	messages: {
		zh: field => field + '必须是四位数字'
	},
	validate: value => {
		return /\d{4}/.test(value)
	}
})


Validator.updateDictionary(dictionary);
const validator = new Validator();
validator.setLocale('zh');
Vue.use(Validate, config)


