<template>
	<div class="wrap">
		<mt-header :title="title"></mt-header>
		<div class="main">
			<div class="bg-white">
	
				<mt-cell title="商品金额">
					<span class="input-tr">
						<CurrencyInput v-model="applyNumber" :placeholder="placeholder" :min="currentData.amtLowLimit" :max="currentData.amtUplimit" v-on:error="isShowTip=true;isApplyNum=false" v-on:success="isShowTip=false;isApplyNum=true" :disabled="disabled" /> 元
					</span>
				</mt-cell>
				<div class="is-danger help-bar" v-if="isShowTip">输入金额必须在{{currentData.amtLowLimit}}-{{currentData.amtUplimit}}之间</div>
				<!-- <StageBox v-on:getCurrentData="showCurrentData" :stage="output.stageNum"></StageBox> -->
				<a class="mint-cell mint-field">
					<div class="mint-cell-left"></div>
					<div class="mint-cell-wrapper">
						<div class="mint-cell-title">
							<span class="mint-cell-text">免息分期</span>
						</div>
						<div class="mint-cell-value">
							<div class="flag-box">
								<span v-for="item in stageArray">
									<input type="radio" v-model="stages" :value="item" />
									<b>{{item}}期</b>
								</span>
							</div>
						</div>
					</div>
					<div class="mint-cell-right"></div>
				</a>
				<div class="fee-tip f12 tr">手续费率：每期仅需{{merFeeRate | formatMoney}}%</div>
			</div>
	
			<div class="cal-box">
				<div class="flex flex-justify border-b">
					<div class="cal-title ">还款测算(元)</div>
				</div>
				<div class="flex flex-justify cal-item">
					<div class="item flex flex-acenter flex-col">
						<p class="f16">{{merPrincipal | formatMoney}}</p>
						<!-- <p class="f16">0.00</p> -->
						<span class="f14">商品本金</span>
	
					</div>
					<div class="flex flex-acenter f20">+</div>
					<div class="item  flex flex-acenter flex-col">
						<p class="f16">{{output.merFee | formatMoney}}</p>
						<!-- <p>30,000.00</p> -->
	
						<span class="f14">手续费</span>
	
					</div>
					<div class="flex flex-acenter f20">=</div>
					<div class="item flex flex-acenter flex-col">
						<p class="f20">{{output.merStageNum | formatMoney}}</p>
						<!-- <p class="font-lg">60,000,00</p> -->
						<span class="f14">每期金额</span>
	
					</div>
				</div>
			</div>
			<div class="agree-box">
				<input type="checkbox" v-model="checked" class="iconfont blue f20" v-bind:class="{'icon-squarecheck': checked, 'icon-square': !checked}">
				<span>我已阅读并同意</span>
				<span class="link" @click="contractPopupVisible=true">《个人消费分期支付合同》</span>
				<!-- <router-link to="/apply">《服务协议》</router-link> -->
	
			</div>
			<div class="button-box">
				<mt-button type="primary" size="large" v-bind:disabled="!checked ||!isApplyNum" @click="nextStep">下一步</mt-button>
			</div>
		</div>
		<mt-popup v-model="contractPopupVisible" :closeOnClickModal="false" position="right" :modal="false">
			<div class="full">
				<Contract v-on:close="contractPopupVisible=false" :data="contractData" />
			</div>
		</mt-popup>
	</div>
</template>

<script>
	import {
		Header,
		Cell
	} from "mint-ui";
	import StageBox from "@/components/stagebox";
	import { InfiniteScroll , MessageBox , Toast} from 'mint-ui';
	import * as LFQCreateOrderService from "@/services/LFQCreateOrder";
	import * as Filters from "@/plugins/filters";
	import CurrencyInput from "@/components/currency";
	import Storage from "@/plugins/storage";
	import Contract from '@/components/contract/';
	
	
	export default {
		components: {
			CurrencyInput,
			Contract
		},
		data() {
			return {
				output: {
					goods_amount: 0,
					lfq_cnt: "6",
					cardFeeRate: 0.0,
					merStageNum: 0.0,
					merFee: 0.0,
					merId: 0,
					open_id: this.GLOBAL.OPENID,
					prdt_no: '',
					lng: this.GLOBAL.LNG,
					lat: this.GLOBAL.LAT,
					lfq_amt: 0
				},
				applyNumber: "",
				stages: "6",
				merPrincipal: 0,
				merFeeRate: 0,
				placeholder: "",
				checked: true,
				isShowTip: false,
				isApplyNum: false,
				currentData: {},
				data: [],
				stageArray: [],
				title: "商户名称",
				disabled: true,
				contractPopupVisible: false,
				// 合同中要显示的字段
				contractData: {
					title: '个人消费分期支付合同'
				}
			};
		},
		methods: {
			stageView() {
				var _this = this;
				LFQCreateOrderService.queryStages({
					'servlet_type': 'query_mer_info',
					'sceneId': _this.GLOBAL.SCENEID
				}).then(function(res) {
					if(res.error_response){
						return MessageBox({
							title: '提示',
							message: res.error_response.resp_msg,
							confirmButtonHighlight : true
						});
					}
					_this.disabled = false;
					_this.data = res.mer_prdt_rate;
					_this.title = res.mer_name;
					_this.output.merId = res.mer_id;
					_this.GLOBAL.PRDTNO = res.prdtNo;
					_this.output.prdt_no = res.prdtNo;
					_this.data.forEach(function(el, index) {
						_this.stageArray.push(el.stageCount);
						if (el.stageCount == this.stages) {
							_this.currentData = _this.data[index];
							_this.placeholder = `目前只支持${_this.currentData.amtLowLimit}-${_this.currentData.amtUplimit}`;
							_this.merFeeRate =
								Math.round(
									Number.parseFloat(_this.currentData.custRate) /
									_this.currentData.stageCount *
									100
								) / 100;
	
						}
					}, _this);
	
				});
			},
			nextStep() {
				if (!this.checked || !this.isApplyNum) {
					return;
				} else {
					this.output.goods_amount = this.applyNumber;
					this.output.lfq_cnt = this.stages;
					this.output.cardFeeRate = this.currentData.custRate;
					Storage.set("apply", this);
					this.$router.push("/c_nextapply");
				}
			},
			//计算
			calRes(apply) {
				this.output.lfq_amt =
					apply * (Number.parseFloat(this.currentData.custRate) / 100 + 1);
				this.output.lfq_amt = Math.round(this.output.lfq_amt * 100) / 100;
				 this.merPrincipal =
        Math.ceil(apply / this.currentData.stageCount * 100) / 100;
        this.output.merFee =  Math.ceil(this.merFeeRate*this.applyNumber)/100;
       this.output.merStageNum = this.merPrincipal + this.output.merFee;
			}
		},
		watch: {
			applyNumber: function(newVl) {
				if (newVl == "") {
					this.isApplyNum = false;
					// this.output.goods_amount = 0;
					this.calRes(0);
					return;
				}
				if (newVl < this.currentData.amtLowLimit) {
					this.isShowTip = true;
					this.isApplyNum = false;
				} else {
					this.isShowTip = false;
					this.isApplyNum = true;
				}
				// this.output.goods_amount = this.applyNumber;
				this.calRes(newVl);
			},
			stages: function(newVl) {
				// console.log(newVl)
				var _this = this;
				// console.log(this.data)
				this.data.forEach(function(el, index) {
					if (newVl == el.stageCount) {
						_this.currentData = _this.data[index];
					}
				}, this);
				this.placeholder = `目前只支持${this.currentData.amtLowLimit}-${this
	        .currentData.amtUplimit}`;
				this.merFeeRate =
					Math.round(
						Number.parseFloat(this.currentData.custRate) /
						this.currentData.stageCount *
						100
					) / 100;
				if (this.applyNumber == "") {
					this.isShowTip = false;
					this.isApplyNum = false;
					return;
				}
				if (this.applyNumber < this.currentData.amtLowLimit) {
					this.isShowTip = true;
					this.isApplyNum = false;
				} else {
					this.isShowTip = false;
					this.isApplyNum = true;
				}
				if (this.applyNumber > this.currentData.amtUplimit) {
					this.applyNumber = this.currentData.amtUplimit;
				}
				this.calRes(this.applyNumber);
			}
		},
		computed: {
	
		},
		mounted() {
			this.$nextTick(() => {
				this.stageView();
			});
		},
		created() {
			// Storage.get('apply', this)
			// this.applyNumber = this.output.goods_amount;
			// this.calRes(this.applyNumber);
			// this.$eventBus.$on('getCurrentData', this.showCurrentData);
		}
	};
</script>


<style lang="less" scoped>
	@import "../../../../styles/variable.less";
	@import "../../../../styles/base.less";
	input {
		text-align: right;
		padding-right: 5px;
	}
	
	.blue {
		color: @blue_2;
	}
	
	.pl-5 {
		padding-left: 5px;
	}
	
	.bg-white {
		background-color: @white_1;
	}
	
	.fee-tip {
		padding: 10px 15px;
		color: @gray_1;
	}
	
	.help-bar {
		border-bottom: 1px solid @gray_3;
	}
	
	.cal-box {
		height: 160px;
		background-color: @blue_2;
		padding: 0 15px;
		color: @white_1;
		.cal-title {
			padding: 20px 0 20px;
			align-items: center; // color: @white_1;
			font-size: 1.6rem;
		}
	}
	
	.cal-item {
		padding: 20px 0;
		p {
			height: 30px;
		}
	}
	
	.agree-box {
		padding: 15px 15px 0 15px;
		font-size: 1.4rem;
		input {
			padding: 0;
			margin: 0 5px 0 0;
			vertical-align: top;
		}
		a {
			color: @blue_2;
		}
	}
</style>



