/**
 * HTTP数据通信模块
 */
import 'whatwg-fetch'
import qs from 'querystring'
import { Toast } from 'mint-ui'
// 缺省请求头
const defaultHeaders = {
	'Accept': 'application/json,text/html,application/xhtml+xml,application/xml;q=0.9,image/webp,image/apng,*/*;q=0.8',
	//'Content-Type': 'application/json' // application/x-www-form-urlencoded
	'Content-Type': 'application/x-www-form-urlencoded'
}
let loading

// 请求参数加工
function mutate(url, { headers, body, query, ...options }) {
	headers = { ...defaultHeaders, ...headers }
	options.headers = headers
	if (body) {
		if (typeof body === 'object') {
			//body = JSON.stringify(body)
			body = qs.stringify(body)
		}
		options.body = body
	}

	if (query) {
		if (typeof query === 'object') {
			query = qs.stringify(query)
		}
		url += (url.indexOf('?') !== -1) ? '&' : '?'
		url += query
	}

	options.credentials = 'omit' // omit,same-origin,include
	options.mode = 'cors' // "same-origin", "cors", "no-cors", "navigate", or "websocket"

	return [url, options]
}

function checkStatus(response) {
	if (response.status >= 200 && response.status < 300) {
		return response
	} else {
		var error = new Error(response.statusText)
		error.response = response
		throw error
	}
}

function parseJSON(response) {
	loading.close()
	return response.json()
}

/**
 * 接口调用服务
 * @param {String} url 接口API（必填）
 * @param {Object} options 请求参数（选填）{headers, body, query, ...options}
 * @return {Object} Promise
 * @throws {Error}
 */
const callService = (url, options = {}) => {
	var duration = -1;
	if (options.body.isShowLoading == false) {
		duration = 1;
	}
	// 请求服务前，加loading动画效果
	loading = Toast({
		message: '加载中...',
		iconClass: 'iconfont icon-loading f50 spin',
		duration: duration,
		className : 'mask'
	});
	

	return window.fetch(...mutate(url, options))
		.then(checkStatus)
		.then(parseJSON)
		.catch(error => {
			console.log(error)
			loading.close()
			Toast({
				message: '系统异常',
				iconClass: 'iconfont icon-error f50',
				duration: 2000,
				className : 'mask'
			})
			throw error
		})

}

export default callService

export const get = (url, args, options = {}) => {
	options.method = 'GET'
	args.t = new Date() * 1
	options.query = args
	return callService(url, options)
}

export const post = (url, args, options = {}) => {
	options.method = 'POST'
	args.t = new Date() * 1
	options.body = args
	return callService(url, options)
}

