<template>
	<div class="wrap">
		<mt-header title="交易结果">
			<router-link to="/c_detail" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
		</mt-header>
		<div class="main" v-if="result=='true'">
			<div class="res-box success-box flex flex-col flex-acenter">
				<div class="icon-box blue_1 tc">
					<span class="icon iconfont icon-success"></span>
				</div>
				<p class="tc f20 blue_1">交易成功</p>
			</div>
			<div class="button-box flex flex-around">
				<mt-button type="primary" plain @click="queryOrder()">交易查询</mt-button>
				<mt-button type="primary" plain @click="$router.push('/c_apply')">返回首页</mt-button>
			</div>
		</div>
		<div class="main" v-if="result=='false'">
			<div class="res-box failed-box fex flex-col flex-jcenter flex-acenter">
				<div class="icon-box  gray_2 tc">
					<span class="icon iconfont icon-error"></span>
				</div>
				<p class="tc f20">交易失败</p>
				<p class="failed-reason  gray_2 tc f14" v-if="reason != 'unknown'">失败原因：{{reason}}</p>
			</div>
		</div>
		<!-- 交易详情弹框 -->
		<mt-popup v-model="orderPopupVisible" :closeOnClickModal="false" position="right" :modal="false">
			<div class="full">
				<QueryTrade v-on:close="closePopup('orderPopupVisible')" :data="orderDetail" />
			</div>
		</mt-popup>
	</div>
</template>

<script>
	import QueryTrade from "@/pages/custom/querytrade";
	import * as LFQCreateOrderService from "@/services/LFQCreateOrder";
	export default {
		components: {
			QueryTrade
		},
		props: ["result", "reason"],
		data() {
			return {
				orderPopupVisible: false,
				orderDetail: {},
				fixed: false
			};
		},
		created: function() {
			// this.result = this.$route.params.result;
			// this.resMessage = this.$route.params.reason || '';
		},
		methods: {
			queryOrder() {
				var self = this;
	
				LFQCreateOrderService.queryOrderbyid({
					servlet_type: "query_orderByOrderId",
					open_id: self.GLOBAL.OPENID,
					serial_no: sessionStorage.getItem("serial_no")
				}).then(function(res) {
					if (res.error_response) {
						return MessageBox({
							title: "提示",
							message: res.error_response.resp_msg,
							confirmButtonHighlight: true
						});
					}
					self.orderDetail = Object.assign(self.orderDetail, res.list[0]);
					self.$eventBus.$emit("updateOrderDetail");
					self.orderPopupVisible = true;
					self.fixed = true;
				});
			},
			closePopup(el) {
				this.$router.push("/c_detail");
				this[el] = false;
				this.fixed = false;
			},
			// 解决父级窗口一起滚动的bug
			fixedPage(e) {
				e.preventDefault();
			}
		}
	};
</script>

<style lang="less" scoped>
	@import "../../../../styles/variable.less";
	@import "../../../../styles/base.less";
	.res-box {
		padding: 30px;
		p {
			line-height: 50px;
		}
	}
	
	.icon {
		font-size: 12rem;
	}
	
	.failed-box {
		p {
			color: @black_1;
		}
		.failed-reason {
			line-height: 30px;
		}
	}
	
	.mint-button--primary.is-plain {
		width: 45%;
	}
</style>



