/*
	创建订单；获取二维码；分页查询；查询单个订单
*/

import * as API from './API'
import { get, post } from '../plugins/http'
import { httpResultProxy } from '../plugins/proxy'

//查询订单消费状态
export function waitOrderResult(args) {
	args.isShowLoading = false;
	let promise = post(API.WAIT_ORDERRESULT, args)
	return httpResultProxy(promise)
}


