/*
	微信配置
*/
import wx from 'weixin-js-sdk';
import * as API from './API'
import { get, post } from '../plugins/http'
import { httpResultProxy } from '../plugins/proxy'

init();

function init() {
	let promise = post(API.WEIXIN, {
		mapUrl :  '/'
	})

	promise.then(function(res){
		wx.config({
			debug: true,
			appId: 'wx24b36936bbf7fcd2',
			timestamp: res.success_response.timestamp,
			nonceStr: res.success_response.nonceStr,
			signature: res.success_response.signature,
			jsApiList: ['chooseImage','previewImage','uploadImage','downloadImage','getLocalImgData','setLocation','getLocation','scanQRCode'] 
		})
		wx.ready(function(){
			alert('wei xin ready')
		})
		wx.error(function(res){
			console.log(res)
		});
	})
}


