/*
	创建订单；获取二维码；分页查询；查询单个订单；查询订单消费状态
*/

import * as API from './API'
import { get, post } from '../plugins/http'
import { httpResultProxy } from '../plugins/proxy'
import QrCodeVO from './model/QrCodeVO'
import StageVO from './model/StageVO'
import OrderVO from './model/OrderVO'

// 创建订单；
export function createOrder(args) {
	let promise = post(API.CREATE_ORDER, args)
	return httpResultProxy(promise)
}

// 获取二维码；
export function recreateOrder(args) {
	args.isShowLoading = false;
	let promise = post(API.RECREATE_ORDER, args)
	return httpResultProxy(promise, QrCodeVO)
}

//分页查询；
export function queryOrder(args) {
	let promise = post(API.QUERY_ORDER, args)
	return httpResultProxy(promise, OrderVO)
}

//查询单个订单
export function queryOrderbyid(args) {
	let promise = post(API.QUERY_ORDERBYORDERID, args)
	return httpResultProxy(promise, OrderVO)
}

//获取商户扣率
export function queryStages(args) {
	let promise = post(API.QUERY_STAGES, args)
	return httpResultProxy(promise, StageVO)
}



