<template>
	<div class="wrap">
		<mt-header title="付款码"></mt-header>
		<div class="main">
			<div class="qr-box">
				<div class="center">
					<div v-show="cutTime > 0">
						<QrCode :value="serialNo" :size="200" />
						<p>有效时间{{cutTime}}秒</p>
					</div>
	
					<div v-show="cutTime <= 0" @click="getNewQrCode">
						<div class="refresh-btn">
							<i class="iconfont icon-refresh"></i>
						</div>
						<p>二维码已失效，请点击刷新</p>
					</div>
	
				</div>
			</div>
			<div class="button-box">
				<mt-button type="primary" size="large" @click="querytrade">分期交易查询</mt-button>
			</div>
		</div>
	</div>
</template>

<script>
	import QrCode from '@/components/qrcode';
	import * as LFQCreateOrderService from '@/services/LFQCreateOrder'
	import * as LFQSearchOrderService from '@/services/LFQSearchOrder'
	var interval, timeout;
	
	function compareTime(t1, t2) {
		t1 = t1.toString();
		t2 = t2.toString();
		var T1 = new Date();
		var T2 = new Date();
	
		T1.setFullYear(t1.slice(0, 4));
		T1.setMonth(t1.slice(4, 6) - 1);
		T1.setDate(t1.slice(6, 8));
		T1.setHours(t1.slice(8, 10));
		T1.setMinutes(t1.slice(10, 12));
		T1.setSeconds(t1.slice(12, 14));
	
		T2.setFullYear(t2.slice(0, 4));
		T2.setMonth(t2.slice(4, 6) - 1);
		T2.setDate(t2.slice(6, 8));
		T2.setHours(t2.slice(8, 10));
		T2.setMinutes(t2.slice(10, 12));
		T2.setSeconds(t2.slice(12, 14));
	
		return (T2.getTime() - T1.getTime()) / 1000
	}
	
	export default {
		components: {
			QrCode
		},
		data() {
			return {
				creatTime: sessionStorage.getItem('creat_qr_time'),
				currentTime: sessionStorage.getItem('creat_qr_time'),
				cutTime: 60,
				serialNo: ''
			}
		},
		methods: {
			// 重新获取二维码
			getNewQrCode() {
				var self = this;
				LFQCreateOrderService.recreateOrder({
					servlet_type: 'recreate_order',
					serial_no: self.serialNo,
					card_6th: sessionStorage.getItem('credit_card_no').toString().slice(-6),
					lng: self.GLOBAL.LNG,
					lat: self.GLOBAL.LAT,
					credit_card_no: sessionStorage.getItem('credit_card_no')
				}).then(function(res) {
	
					if (res.error_response) {
						return MessageBox({
							title: '提示',
							message: res.error_response.resp_msg,
							confirmButtonHighlight: true
						});
					}
	
					self.$router.push('/c_payment/' + res.serial_no);
					sessionStorage.setItem('creat_qr_time', res.res_timestamp);
					self.creatTime = self.currentTime = res.res_timestamp;
					self.serialNo = res.serial_no;
					self.cutTime = 60;
					self.cutDown()
				});
			},
			cutDown() {
				interval = setInterval(() => {
					if (this.cutTime > 0) {
						this.cutTime--;
					} else {
						clearInterval(interval);
					}
				}, 1000);
			},
			querytrade() {
				this.$router.push('/c_detail');
			},
			// 等待后端返回交易结果
			waitOrderResult() {
				var self = this;
				LFQSearchOrderService.waitOrderResult({
					open_id: self.GLOBAL.OPENID,
					serial_no: self.serialNo
				}).then(function(res) {
	
					// 每次请求后，都获取一下系统时间
					self.currentTime = res.res_time;
	
					var timeLength = compareTime(self.creatTime, self.currentTime);
	
					// 判断如果 大于60秒，则验证码超时
					if (timeLength > 60) {
						self.cutTime = 0;
					}
	
					// 如果还是待消费状态，则3秒后再发起查询
					if (res.msg == '01') {
						return timeout = setTimeout(() => {
							self.waitOrderResult()
						}, 3000);
					}
					if (res.msg == '03') {
						sessionStorage.setItem('serial_no', self.serialNo);
						self.$router.push('/c_payresult?result=true');
					} else if (res.msg == '04') {
						self.$router.push('/c_payresult?result=false&reason=此订单已退货');
					} else if (res.msg == '99') {
						self.$router.push('/c_payresult?result=false&reason=unknown');
					} else if (res.msg == '订单不存在') {
						self.$router.push('/c_payresult?result=false&reason=订单不存在');
					}
				});
			}
		},
		created: function() {
			this.serialNo = this.$route.params.serialNo;
			this.cutDown();
			// 页面打开后，等2秒再发起请求
			setTimeout(() => {
				this.waitOrderResult();
			}, 2000);
		},
		beforeDestroy: function() {
			//如果在等待扫描二维码的过程中，离开此页，则不再请求
			clearTimeout(timeout);
			clearInterval(interval);
		}
	}
</script>

<style lang="less" scoped>
	@import '../../../../styles/variable.less';
	.qr-box {
		padding: 40px;
		background-color: @white_1;
		p {
			font-size: 1.6rem;
			padding-top: 10px;
			color: @gray_2;
		}
	}
	
	.refresh-btn {
		width: 200px;
		height: 200px;
		margin: 0 auto;
		//position: absolute;
		text-align: center;
		line-height: 200px;
		background: @white_1;
		.iconfont {
			font-size: 16rem;
			color: @gray_3;
		}
	}
</style>



