<template>
	<div class="wrap">
		<mt-header title="填写资料">
			<router-link to="/b_managerform" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
		</mt-header>
		<div class="main">

            <mt-button type="primary" size="large" @click="scanQRCode">扫一扫</mt-button>

            <mt-cell title="营业执照">

                <!-- <FileUpload 
                    url="http://jsonplaceholder.typicode.com/posts" 
                    action="POST"
                    v-on:fileUploadProgress="fileUploadProgress" 
                    v-on:fileUploadStart="fileUploadStart"
                    v-on:fileUploadFinish="fileUploadFinish" 
                    v-bind:multiple="false">
                </FileUpload> -->
            </mt-cell>
            <mt-cell title="组织机构代码证">
               <FileUpload 
                    url="http://jsonplaceholder.typicode.com/posts" 
                    action="POST"
                    v-on:fileUploadProgress="fileUploadProgress" 
                    v-on:fileUploadStart="fileUploadStart"
                    v-on:fileUploadFinish="fileUploadFinish" 
                    v-bind:multiple="false">
                </FileUpload>
            </mt-cell>
            <mt-cell title="税务登记证">
               <FileUpload 
                    url="http://jsonplaceholder.typicode.com/posts" 
                    action="POST"
                    v-on:fileUploadProgress="fileUploadProgress" 
                    v-on:fileUploadStart="fileUploadStart"
                    v-on:fileUploadFinish="fileUploadFinish" 
                    v-bind:multiple="false">
                </FileUpload>
            </mt-cell>
            <p class="p-15 f14 gray">如您的企业已完成三证合一，请均上传营业执照</p>

            <mt-cell title="法人身份证正面">
               <FileUpload 
                    url="http://jsonplaceholder.typicode.com/posts" 
                    action="POST"
                    v-on:fileUploadProgress="fileUploadProgress" 
                    v-on:fileUploadStart="fileUploadStart"
                    v-on:fileUploadFinish="fileUploadFinish" 
                    v-bind:multiple="false">
                </FileUpload>
            </mt-cell>
            <mt-cell title="法人身份证反面">
               <FileUpload 
                    url="http://jsonplaceholder.typicode.com/posts" 
                    action="POST"
                    v-on:fileUploadProgress="fileUploadProgress" 
                    v-on:fileUploadStart="fileUploadStart"
                    v-on:fileUploadFinish="fileUploadFinish" 
                    v-bind:multiple="false">
                </FileUpload>
            </mt-cell>
            <mt-cell title="行业经营许可证（如有）">
               <FileUpload 
                    url="http://jsonplaceholder.typicode.com/posts" 
                    action="POST"
                    v-on:fileUploadProgress="fileUploadProgress" 
                    v-on:fileUploadStart="fileUploadStart"
                    v-on:fileUploadFinish="fileUploadFinish" 
                    v-bind:multiple="false">
                </FileUpload>
            </mt-cell>
            <mt-cell title="品牌经营许可证（如有）">
               <FileUpload 
                    url="http://jsonplaceholder.typicode.com/posts" 
                    action="POST"
                    v-on:fileUploadProgress="fileUploadProgress" 
                    v-on:fileUploadStart="fileUploadStart"
                    v-on:fileUploadFinish="fileUploadFinish" 
                    v-bind:multiple="false">
                </FileUpload>
            </mt-cell>
            
            <div class="btn">
                <router-link to="/b_managerphoto">
				    <mt-button type="primary" size="large">提交</mt-button>
                </router-link>
			</div>
		</div>
	</div>
</template>

<script>
    import Field from '@/components/field';
    import FileUpload from '@/components/fileupload';
    import wx from 'weixin-js-sdk';
	export default {
        props: [],
        components : {
            Field,FileUpload
        },
        data(){
            return {
                progress: 0,
                output : {
                    cust_name : '',
                    creditCard : ''
                }
            }
        },
        methods:{
            fileUploadStart(e) {
                // start event
                console.log(e);
            },
            fileUploadFinish(e) {
                // finish event
                console.log(e);
            },
            fileUploadProgress(e) {
                this.progress = e;
            },
            scanQRCode(){

                wx.scanQRCode({
                    needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                    scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
                    success: function (res) {
                        console.log(res)
                        var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
                    }
                });
            }
        },
        created(){
            var self = this;

        }
	}
</script>

<style lang="less" scoped>
	.btn{
        margin: 15px;
    }
</style>



