/*
	创建订单；获取二维码；分页查询；查询单个订单
*/

import * as API from './API'
import { get, post } from '../plugins/http'
import { httpResultProxy } from '../plugins/proxy'
// import QrCodeVO from './model/QrCodeVO'


// 发送短信获取验证码
export function sendSms(args) {
	let promise = post(API.SEND_SMS, args)
	return httpResultProxy(promise)
}



