<template>
	<div class="main">
		<mt-header title="扣款记录">
            <mt-button icon="back" slot="left" @click="close">返回</mt-button>
        </mt-header>
        <mt-cell title="订单编号" :value="serialno"></mt-cell>
        <div class="page-infinite">
            <div class="page-infinite-wrapper" ref="wrapper" :style="{ maxHeight: wrapperHeight + 'px' }">
                <div class="page-infinite-list record-list" >
                    <div v-for="(item,index) in list" class="page-infinite-listitem" v-bind:class="{finished:item.respcode=='0001'}">
                        <mt-cell :title="item.transAmt | formatMoney" :label="item.respmsg" >
                            <div class="f14 tr">
                                <div>第{{item.stageCount}}期</div>
                                <div class="mint-cell-label">{{item.instalmentTime.toString() | date}}</div>
                            </div>
                        </mt-cell>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { InfiniteScroll } from 'mint-ui';

export default {
	props : ['data'],
	components: {
       
	},
	data() {
		return {
			list: [],
			serialno : '',
			wrapperHeight: 0
		}
	},
	methods: {
        close(){
            this.serialno = '';
            this.list = [];
            this.$emit('close')
        },
		// 解决父级窗口一起滚动的bug
        fixedPage(e){
            e.preventDefault();
		}
	},
	created() {
		var self = this;
		self.$eventBus.$on('updateRecordDetail',function(){
			self.serialno = self.data.serialNo;
            self.list = self.data.list;
            self.$refs.wrapper.scrollTo(0,0);
		});
	},
	mounted(){
        this.wrapperHeight = document.documentElement.clientHeight - 100;
    }
}
</script>

<style lang="less" rel="stylesheet/less" scoped>
    @import '../../../../styles/variable.less';
    .page-infinite{
        -webkit-overflow-scrolling: touch;
    }
    .page-infinite-wrapper{
        overflow: auto;
    }
	.record-list{
        padding-left:35px;
        >div{
            position: relative;
            &:before{
                content: "";
                width: 11px;
                height: 11px;
                display: block;
                position: absolute;
                left: -15px;
                top: 40%;
                background:@gray_3;
                border-radius: 50%;
                z-index: 0;
            }
            &:after{
                content: "";
                width: 1px;
                height: 60%;
                display: block;
                position: absolute;
                left: -10px;
                top: 70%;
                background:@gray_3;
                border-radius: 50%;
                z-index: 0;
            }
            &.finished{
                &:before{
                     background:@blue_1;
                }
                &:after{
                     background:@blue_1;
                }
            }
        }
    }
</style>



