<template>
	<div>
		<a class="upload-btn">
			<input type="file" accept="image/*" @change="onFileChange" :multiple="multiple">
			点击上传
		</a>
	</div>
</template>
<script>
	export default {
		name: 'fileupload',
		props: {
			// Multiple file toggle
			// False by Default
			multiple: {
				type: Boolean,
				default: false,
			},
			// Target url for file upload
			// Empty String by Default
			url: {
				type: String,
				default: '',
			},
			// Request type
			// POST by Default
			action: {
				type: String,
				default: 'POST',
			},
		},
		data() {
			return {
				files: null,
			};
		},
		methods: {
			// Emits progress 
			progress(e) {
				if (e.lengthComputable) {
					const progress = Math.round((e.loaded / e.total) * 100);
					this.$emit('fileUploadProgress', progress);
				} else {
					this.$emit('fileUploadProgress', false);
				}
			},
			// Listens for file upload changes
			// Checks if multiple
			// Create FormData
			// $emits start upload
			// $emits finish upload
			// $emits progress
			onFileChange(e) {
				const files = e.target.files || e.dataTransfer.files;
				if (!files.length) {
					return;
				}
				const formData = new FormData();
				if (this.multiple) {
					for (let i = 0; i < files.length; i += 1) {
						formData.append('file[]', files[i]);
					}
				} else {
					this.file = files[0];
					formData.append(this.file.name, this.file);
				}
				const request = new XMLHttpRequest();
				request.open(this.action, this.url);
				request.onloadstart = () => {
					this.$emit('fileUploadStart', e);
				};
				request.onloadend = () => {
					this.$emit('fileUploadFinish', e);

					alert(1)


				};
				request.upload.onprogress = this.progress;
				request.send(formData);
			},
		},
	};

</script>
<style lang="less" scoped>
	@import "../../../styles/variable.less";
	.upload-btn{
		border: 1px solid @blue_1;
		color: @blue_1;
		padding: 0px 20px;
		line-height: 32px;
		font-size: 1.4rem;
		display: inline-block;
		border-radius: 4px;
	}
	input{
		position: absolute;
		font-size: 100px;
		right: 0;
		top: 0;
		opacity: 0;
	}
</style>



