import Vue from 'vue'

Vue.filter('formatMoney', function (value, currency = '', decimals = 2) {
    value = parseFloat(value)
    if (!isFinite(value) || (!value && value !== 0)) return ''
    var stringified = Math.abs(value).toFixed(decimals)
    var _int = stringified.slice(0, -1 - decimals)
    var i = _int.length % 3
    var head = i > 0 ? (_int.slice(0, i) + (_int.length > 3 ? ',' : '')) : ''
    var _float = stringified.slice(-1 - decimals)
    var sign = value < 0 ? '-' : ''
    return sign + currency + head + _int.slice(i) + _float
});

Vue.filter('orderState', function (value) {
    var strCn = '';
    switch(value){
        case '01':
            strCn = '待消费'
        break;
        case '02':
        case '03':
            strCn = '分期成功'
        break;
        case '04':
            strCn = '已退货'
        break;
        case '05':
            strCn = '已撤销'
        break;
        case '99':
        case 99:
            strCn = '分期失败'
        break;
    }
    return strCn;
})

Vue.filter('time', function (str) {
    return str.slice(0,4) + '/' + str.slice(4,6) + '/' + str.slice(6,8) + ' ' + str.slice(9,11) + ':' + str.slice(11,13) + ':' + str.slice(13,15)
    
})

Vue.filter('date', function (str) {
    return str.slice(0,4) + '/' + str.slice(4,6) + '/' + str.slice(6,8);
    
})


