export default class OrderVO {
	constructor(data) {
		this.serial_no = data.serial_no;
		// 分期查询
		this.list = data.lfqorderInfo_arrays.lfqorderInfo;
		
		// 扣款记录
		if(data.lfqRepayNotices_arrays){
			this.record_list = data.lfqRepayNotices_arrays.lfqRepayNotices;
		}
		
	}
}



