<template>
	<div class="wrap">
		<mt-header title="用户设置">
			<router-link to="/b_comhome" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
		</mt-header>
		<div class="main">
			<div class="">
				<mt-cell title="账户安全">
				</mt-cell>
				<mt-cell title="手机号码">
					<div>
						<span>134****2222</span>
						<mt-button type="primary" plain @click.native="showPopup('telPopupVisible')">修改</mt-button>
					</div>
	
				</mt-cell>
				<mt-cell title="登录密码">
					<div>
						<span></span>
						<mt-button type="primary" plain @click.native="showPopup('psdPopupVisible')">修改</mt-button>
					</div>
	
				</mt-cell>
	
			</div>
			<div class="mt-15">
				<mt-cell title="收银员管理">
				</mt-cell>
				<mt-cell :title="title">
					<div>
						<span>134****2222</span>
						<mt-button type="primary" plain @click.native="showPopup('delPopupVisible')">删除</mt-button>
					</div>
				</mt-cell>
				<mt-cell :title="title">
					<div>
						<span>134****2222</span>
						<mt-button type="primary" plain @click.native="showPopup('delPopupVisible')">删除</mt-button>
					</div>
				</mt-cell>
			</div>
			<div class="add-box">
				<p class="f18 blue_1 tc" @click="showPopup('addPopupVisible')"><i class="iconfont icon-zengjia"></i> 添加</p>
			</div>
	
		</div>
		<!-- 修改手机号弹框 -->
		<mt-popup v-model="telPopupVisible" :closeOnClickModal="false" position="right">
			<div class="full">
				<ComTelModel v-on:close="closePopup('telPopupVisible')" />
			</div>
		</mt-popup>
		<!-- 修改密码弹框 -->
		<mt-popup v-model="psdPopupVisible" :closeOnClickModal="false" position="right">
			<div class="full">
				<ComPSDModel v-on:close="closePopup('psdPopupVisible')" />
			</div>
		</mt-popup>
		<!-- 删除营业员弹框 -->
		<mt-popup v-model="delPopupVisible" :closeOnClickModal="false" class="pop-box">
			<div class="content">
				<p class="tc f20 ">是否删除此信息</p>
				<div class="button-box flex flex-around">
					<mt-button type="primary" plain @click.native="closePopup('delPopupVisible')">取消</mt-button>
					<mt-button type="primary" @click.native="delSubmit('0001')">确定</mt-button>
				</div>
			</div>
		</mt-popup>
		<!-- 增加营业员弹框 -->
		<mt-popup v-model="addPopupVisible" :closeOnClickModal="false" class="pop-box">
			<div class="flex flex-jcenter flex-acenter title border-b">
				<div>添加收银员</div>
			</div>
			<div class="content">
				<div class="psd-box">
					<div class="input-box border_b">
						<input type="text" placeholder="请输入姓名">
					</div>
					<div class="input-box border_b">
						<input type="num" placeholder="请输入密码">
					</div>
				</div>
				<div class="button-box flex flex-around">
					<mt-button type="primary" plain @click.native="closePopup('addPopupVisible')">取消</mt-button>
					<mt-button type="primary" @click.native="delSubmit('0001')">确定</mt-button>
				</div>
			</div>
		</mt-popup>
	</div>
</template>

<script>
	import ComTelModel from '@/pages/business/commercial/comtelmodel';
	import ComPSDModel from '@/pages/business/commercial/compsdmodel';
	import {
		MessageBox
	} from 'mint-ui';
	export default {
		components: {
			ComTelModel,
			ComPSDModel
		},
		data() {
			return {
				title: '0001 田瑞丰',
				telPopupVisible: false,
				psdPopupVisible: false,
				delPopupVisible: false,
				addPopupVisible: false
			}
		},
		methods: {
			showPopup(el) {
				this[el] = true;
				this.fixed = true;
			},
			closePopup(el) {
				this[el] = false;
				this.fixed = false;
			},
			// 解决父级窗口一起滚动的bug
			fixedPage(e) {
				e.preventDefault();
			},
			delSubmit(id) {
				console.log(id)
			}
		}
	};
</script>

<style lang="less" scoped>
	@import "../../../../../styles/variable.less";
	@import "../../../../../styles/base.less";
	.add-box {
		background-color: #fff;
		padding: 16px 0;
		span {
			padding-right: 10px;
		}
	}
	
	.pop-box {
		padding: 10px 0 20px;
	}
	
	.content {
		.mint-button--primary {
			width: 45%;
		}
	}
	
	.psd-box {
		padding: 0 10px;
	}
</style>



