<template>
	<div class="wrap">
		<mt-header title="交易明细">
			<mt-button icon="back" slot="left" @click="close">返回</mt-button>
		</mt-header>
		<div class="main">
			<mt-cell title="订单信息">
	
			</mt-cell>
			<mt-cell title="订单号">
				<span>12345678901234567890</span>
			</mt-cell>
			<mt-cell title="订单状态" value="分期成功">
				<div>
					<span>分期成功</span>
					<mt-button type="primary" plain @click.stop.prevent="goodRtn">退货</mt-button>
				</div>
	
			</mt-cell>
			<mt-cell title="交易金额">
				<span>5,000.00</span>
			</mt-cell>
			<mt-cell title="商品金额">
				<span>5,000.00</span>
			</mt-cell>
			<mt-cell title="交易时间">
				<span>2016-1-1 12:55:40</span>
			</mt-cell>
			<mt-cell title="交易类型">
				<span>消费</span>
			</mt-cell>
			<mt-cell title="交易卡号">
				<span>1234***9012</span>
			</mt-cell>
			<mt-cell title="终端号">
				<span>123456789012345</span>
			</mt-cell>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {};
		},
		methods: {
			close() {
				this.$emit("close");
			},
			goodRtn() {
				this.$emit("goodRtn");
			},
			// 解决父级窗口一起滚动的bug
			fixedPage(e) {
				e.preventDefault();
			}
		}
	};
</script>

<style lang="less" scoped>
	
</style>



