<template>
    <div class="wrap">
        <mt-header title="分期消费">
            <mt-button icon="back" slot="left" @click="close">返回</mt-button>
        </mt-header>
        <div class="main">
            <div class="mt-15">
                <mt-cell title="交易金额">
                    <!-- <span><input v-model="payNum" type="num" placeholder="请输入金额(元)" ></span>  -->
                    <CurrencyInput v-model="payNum" placeholder="请输入金额(元)" :min="low" :max="up" v-on:error="isShowTip=true" v-on:success="isShowTip=false" />
                </mt-cell>
                <div class="is-danger help-bar" v-if="isShowTip">输入金额必须在1-1000000之间</div>
                <div class="button-box">
                    <mt-button type="primary" size="large" v-bind:disabled="!isApplyNum" >扫码</mt-button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import CurrencyInput from "@/components/currency";
export default {
   components: {
    CurrencyInput
  },
  data(){
      return {
          isApplyNum:false,
          payNum:'',
          isShowTip:false,
          low:1,
          up:100000
      }
  },
  methods:{
       close(){
            this.$emit('close')
        },
		// 解决父级窗口一起滚动的bug
        fixedPage(e){
            e.preventDefault();
        }
  },
    watch:{
       payNum (newVl){
           if(newVl != '' || newVl !=0){
               this.isApplyNum = true;
           }else{
               this.isApplyNum = false;
           }
       } 
    }
}
</script>
<style lang="less" scoped>
@import "../../../../styles/variable.less";
@import "../../../../styles/base.less";

</style>



