<template>
	<div class="wrap">
        <mt-header :title="title">
            <router-link to="/" slot="left">
                <mt-button icon="back">返回</mt-button>
            </router-link>
            <mt-button icon="more" slot="right"></mt-button>
        </mt-header>
        <div class="main">
            <mt-button type="primary" size="large" @click="showAlert">primary</mt-button>
            <mt-button type="default" size="large" @click="showToast">default</mt-button>
            <button class="mint-button mint-button--success mint-button--normal mint-button--large" @click="saveToStorage">
                <label class="mint-button-text">set Item</label>
            </button>
            <router-link to="/detail">
                <mt-button type="default" size="large">详情</mt-button>
            </router-link>
            <mt-button type="danger" size="large" @click="showPasswordPopup">danger</mt-button>

            <Field v-model="output.name" label="用户名" placeholder="用户名" validate="required" name="username" />
            <Field v-model="output.email" label="邮箱"  placeholder="邮箱" validate="required|email" name="email" />
            <a class="mint-cell mint-field">
                <div class="mint-cell-left"></div>
                <div class="mint-cell-wrapper">
                    <div class="mint-cell-title">
                        <span class="mint-cell-text">免息分期</span>
                    </div>
                    <div class="mint-cell-value">
                        <div class="flag-box">
                            <span>
                                <input type="radio" v-model="output.stages" value="3" />
                                <b>3期</b>
                            </span>
                            <span>
                                <input type="radio" v-model="output.stages" value="6" />
                                <b>6期</b>
                            </span>
                            <span>
                                <input type="radio" v-model="output.stages" value="12" />
                                <b>12期</b>
                            </span>
                        </div>
                    </div>
                </div>
                <div class="mint-cell-right"></div>
            </a>
            
            <mt-checklist
                title="复选框列表"
                v-model="output.checklist"
                :options="['选项A', '选项B', '选项C']">
            </mt-checklist>

            <mt-radio
                title="单选框列表"
                v-model="output.radiolist"
                :options="['选项A', '选项B', '选项C']">
            </mt-radio>


            <PickAddress 
                :address="output.address" 
                :detail="output.detail" />

            <Wrap 
                :address="output.address" 
                :detail="output.detail" />

            <div @click="showQRcode">
                <mt-cell title="待消费" label="2016-05-12 12:30:22" is-link>
                    <div class="money">
                        <div>1,905,000.00</div>
                        <div>0/12期</div>
                    </div>
                    <i class="iconfont icon-qrcode"></i>
                </mt-cell>
            </div>
        </div>

        <!-- 弹出框 -->
        <mt-popup v-if="output.popupVisible" v-model="output.popupVisible"  :closeOnClickModal="error">
            <div @touchmove="fixedPage">
                <Entercard6 v-on:close="output.popupVisible=false" title="请输入信用卡号后六位" />
            </div>
        </mt-popup>
        <mt-popup v-if="output.qrPopupVisible" v-model="output.qrPopupVisible"  :closeOnClickModal="error">
            <div @touchmove="fixedPage">
                <QrCode value="lancer lin" :size="200" />
                <div class="mint-msgbox-btns">
                    <button class="mint-msgbox-btn mint-msgbox-confirm" @click="output.qrPopupVisible=false" >关闭</button>
                </div>
            </div>
        </mt-popup>
        <!-- 弹出框 -->
    </div>
</template>

<script>
import Vue from  'vue';
import Storage from  '@/plugins/storage';
import Entercard6 from  '@/components/entercard6';
import Field from  '@/components/field';
import PickAddress from  '@/components/address';
import QrCode from  '@/components/qrcode';
import * as mint from 'mint-ui';

export default {
	components: {
        Entercard6,Field,PickAddress,QrCode
	},  
	data() {
		return {
            error : false,
            title : '首页',
            selected : '外卖',
            output : {
                popupVisible : false,
                qrPopupVisible : false,
                email : '',
                name : '',
                stages : '6',
                address : '',
                detail : '',
                checklist : [],
                radiolist : ''
            }
        }
	},
	methods: {
        showToast : function(){
            mint.Toast({
                message: '操作成功',
                iconClass: 'iconfont icon-check f50',
                duration: 1000
            });
        },
        showAlert : function(){
            mint.MessageBox({
                title: '提示',
                message: '确定执行此操作?',
                showCancelButton: true,
                confirmButtonHighlight : true
            }).then(function(res){
                console.log(res)
            })
        },
        showPasswordPopup : function(){
            this.output.popupVisible = true;
        },
        showQRcode : function(){
            this.output.qrPopupVisible = true;
        },
        saveToStorage : function(){
            Storage.set('home',this);
        },
        showAddress (res){
            this.output.address = res;
        },
        showAddressDetail(res){
            this.output.detail = res;
        },
        // 解决父级窗口一起滚动的bug
        fixedPage(e){
            e.preventDefault();
        }
    },
    created(){
        this.$eventBus.$on('getAddress',this.showAddress);
        this.$eventBus.$on('getDetail',this.showAddressDetail);
        this.$nextTick(function () {
            Storage.get('home',this);
        });
    }
}
</script>

<style lang="less" rel="stylesheet/less">

</style>



