import { Toast } from 'mint-ui'
/**
 * 对http请求参数代理
 */
export function argumentsProxy(vo) {
	if (vo) {
		if (typeof vo.revert === 'function') {
			return vo.revert()
		} else {
			return vo
		}
	} else {
		return null
	}
}

/**
 * 对http结果代理
 */
export function httpResultProxy(resultPromise, VO = null) {
	return new Promise((resolve, reject) => {
		resultPromise.then(res => {
			if (res) {
				if (res.success_response) {
					//if (res.rlt === 'true') {
					resolve(mapVO(VO, res.success_response))
				} else if (res.error_response) {
					resolve(res)
				} else if (res.msg) { // 非servlet的接口 只返回一个 msg字段
					resolve(res)
				} else {
					reject(new Error(res.msg))
				}
			} else {
				reject(new Error('服务器返回数据异常'))
			}
		}, (error) => {
			throw error
		}).catch(error => {
			console.log(error)
			Toast({
				message: '系统异常',
				iconClass: 'iconfont icon-error f50',
				duration: 2000,
				className : 'mask'
			})
			reject(new Error('系统异常！'))
			// if (error.response) {
			// 	reject(new Error('服务器异常！'))
			// } else {
			// 	reject(new Error('系统异常！'))
			// }
		})
	})
}

function mapVO(VO, data) {
	if (VO) {
		if (Array.isArray(data)) {
			let arr = []
			for (let item of data) {
				arr.push(new VO(item))
			}
			return arr
		} else {
			return new VO(data)
		}
	} else {
		return data
	}
}



