const Storage = {
    set(key,self){
        sessionStorage.setItem(key,JSON.stringify(self.output));
    },
    get(key,self){
        self.output = Object.assign({}, self.output,JSON.parse(sessionStorage.getItem(key)));
    },
    remove(key){
        sessionStorage.removeItem(key);
    },
    clear(){
        sessionStorage.clear();
    }
}

export default Storage;


