<template>
	<div class="wrap">
		<mt-header title="申请分期">
			<router-link to="/c_apply" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
		</mt-header>
		<div class="main">
			<Field v-model="output.cust_name" label="用户名" validate="required|name" name="username" placeholder="请输入用户名" type="text" maxlength='4' />
			<Field v-model="output.id_no " label="身份证号" validate="required|idno" name="idno" placeholder="18位证件号,末位X以*代替" type="tel" maxlength='18' />
			<Field v-model="output.creditCard" format="crecard" label="信用卡号" validate="required|credit_card" name="crecardno" placeholder="请输入信用卡号" type="tel" maxlength='16'></Field>
			<Field v-model="output.cvn2Num" label="CVN2" validate="required|cvnno" name="cvn2no" placeholder="信用卡背面签名条三位数字" type="tel" maxlength='3'></Field>
			<Field v-model="output.timeNum" label="有效期" validate="required|time|validtime" name="timeno" placeholder="MMYY,2017年3月到期即0317" type="tel" maxlength='4'></Field>
			<Field v-model="output.hp_no" label="手机号码" validate="required|telno" name="telno" placeholder="银行预留手机号" type="tel" maxlength='11'></Field>
	
			<SendSms v-model="output.verify_code" label="验证码" :hpNo="output.hp_no" v-on:update="function(v){output.verify_code = v}" />
	
			<div class="button-box">
				<mt-button type="primary" size="large" v-bind:disabled="isUnValid" @click="submitApply">提交申请</mt-button>
			</div>
			<mt-popup v-model="popupVisible" class="pop-box">
				<div class="flex flex-jcenter flex-acenter title border-b">
					<div>确认分期</div>
					<div class="title-right" @click="popupVisible=false">×</div>
				</div>
				<div class="content">
					<mt-cell title="商品金额">
						<span>{{apply.output.goods_amount | formatMoney}}元</span>
					</mt-cell>
					<mt-cell title="分期期数">
						<span>{{apply.output.lfq_cnt}}期</span>
					</mt-cell>
					<mt-cell title="每期预计还款金额">
						<span>{{apply.output.merStageNum | formatMoney}}元</span>
	
					</mt-cell>
					<p class="tr f14">包含每期手续费{{apply.output.merFee | formatMoney}}元</p>
	
				</div>
				<div class="button-box">
					<mt-button type="primary" size="large" @click="stageSubmit">确认</mt-button>
				</div>
				<div class="tips">
					<p>分期成功之后，分期期数、金额无法修改或取消。还款金额截取至分，余额计入最后一期。</p>
				</div>
	
	
			</mt-popup>
		</div>
	
	</div>
</template>

<script>
	// import PickAddress from '@/components/address';
	import Field from '@/components/field';
	import Storage from '@/plugins/storage';
	import SendSms from '@/components/sendsms';
	import { MessageBox, Toast } from 'mint-ui';
	import * as Filters from '@/plugins/filters';
	import * as LFQCreateOrderService from '@/services/LFQCreateOrder';
	import * as LFQSendSMS from '@/services/LFQSendSMS';
	
	var errorsCollection = {
		username: false,
		idno: false,
		crecardno: false,
		cvn2no: false,
		timeno: false,
		telno: false,
		verifycode: false
	}
	
	export default {
		components: {
			Field,
			SendSms
		},
		data() {
			return {
				output: {
					cust_name: '',
					id_no: '',
					creditCard: '',
					cvn2Num: '',
					timeNum: '',
					hp_no: '',
					verify_code: '',
					servlet_type: 'create_order'
					// address: '',
					// detail: '',
				},
				apply: {},
				verifyBtnText: '获取验证码',
				isUnGetVerify: false,
				getCodeTime: 60,
				popupVisible: false,
				isUnValid: true
			}
		},
		methods: {
			submitApply() {
				if (this.isUnValid == true) {
					return
				}
				// Storage.set('applyNext', this);
				this.popupVisible = true;
			},
			verify() {
				if (!errorsCollection.telno) {
					Toast({
						message: '请输入有效的手机号码',
						position: 'center',
						duration: 3000
					});
					return;
				}
	
				if (this.isUnGetVerify == true) {
					return;
				} else {
					var _this = this;
					LFQSendSMS.sendSms({
						hp_no: _this.output.hp_no,
						openid: _this.GLOBAL.OPENID,
						prdt_no: _this.GLOBAL.PRDTNO
					}).then(function(res) {
						_this.isUnGetVerify = true;
						Toast({
							message: '验证码已发送',
							position: 'center',
							duration: 3000
						});
	
					}, this);
					var getCodeTime = 60;
					this.verifyBtnText = `重新获取${getCodeTime}s`
					var setIntervarHandle = setInterval(function() {
						getCodeTime--;
						_this.verifyBtnText = `重新获取${getCodeTime}s`
						if (getCodeTime == 0) {
							_this.verifyBtnText = '获取验证码'
							clearInterval(setIntervarHandle);
							_this.isUnGetVerify = false;
							getCodeTime = 60;
						}
					}, 1000)
				}
			},
			stageSubmit() {
				this.output.id_no = this.output.id_no.replace(/\*/g, 'X');
				var parameter = Object.assign(this.apply.output, this.output);
				var _this = this;
				LFQCreateOrderService.createOrder(parameter).then(function(res) {
					if(res.error_response){
						return MessageBox({
							title: '提示',
							message: res.error_response.resp_msg,
							confirmButtonHighlight : true
						});
					}
					_this.output.id_no = _this.output.id_no.replace(/X/g, '*');
					_this.popupVisible = false;
					if (res.resp_code) {
						if (res.resp_code == '0000') {
							//  Storage.remove('applyNext');
							//  Storage.remove('apply');
							Toast({
								message: '申请成功',
								position: 'center',
								duration: 500
							});
							sessionStorage.setItem('serial_no', res.serial_no);
							sessionStorage.setItem('credit_card_no', _this.output.creditCard)
							_this.$router.push('/c_payment/' + res.serial_no);
						} else if (res.resp_code != '0000') {
							MessageBox.alert(res.resp_msg, '提示');
						}
					} else {
						var tipmsg = '';
						var resmsg = '';
						if (res.error_response != undefined) {
							resmsg = res.error_response.resp_code;
							tipmsg = res.error_response.resp_msg ? res.error_response.resp_msg : res.error_response.sub_msg;
						} else if (res.user_acct_response != undefined) {
							resmsg = res.user_acct_response.is_black;
						} else {
							tipmsg = "当前服务请求出错";
						}
	
						if (resmsg == 'WRONG0001') {
							tipmsg = "短信验证码出错";
						} else if (resmsg == 'WORNG9999') {
							tipmsg = "申请服务异常";
						}
						MessageBox.alert(tipmsg, '提示');
					}
	
				}, this);
	
	
	
	
			},
			unValid(result) {
	
				var self = this;
				errorsCollection = Object.assign(errorsCollection, JSON.parse(result));
				for (var item in errorsCollection) {
					if (!errorsCollection[item]) {
						self.isUnValid = true;
						break;
					} else {
						self.isUnValid = false;
					}
				}
	
			}
		},
		mounted() {
			// Storage.get('applyNext', this);
	
		},
		created() {
			Storage.get('apply', this.apply)
			if (!this.apply.output.goods_amount) {
				//如果没有金额 则回到扫码后的界面
				this.$router.push('/c_apply')
			}
			// this.$eventBus.$on('getAddress',this.showAddress);
			// this.$eventBus.$on('getDetail',this.showAddressDetail);
			this.$eventBus.$on('errorsChanged', this.unValid);
		}
	}
</script>

<style lang="less" scoped>
	@import '../../../../styles/variable.less';
	@import '../../../../styles/base.less';
	.content {
		span {
			color: @black_1;
		}
	}
	
	p {
		padding: 10px 15px 0;
		line-height: 20px;
		color: @gray_2;
	}
	
	.disabled {
		color: @black_1;
		background-color: @gray_3;
	}
</style>




