<template>
	<div class="wrap" v-bind:class="{fixedbody:fixed}">
		<mt-header title="分期查询"></mt-header>
		<div class="main">
			<div class="page-loadmore-wrapper" ref="wrapper" v-bind:class="{fixedbody:fixed}" :style="{ maxHeight: wrapperHeight + 'px' }">
				<mt-loadmore 
					:top-method="loadTop" 
					@translate-change="translateChange" 
					@top-status-change="handleTopChange" 
					:bottom-method="loadBottom" 
					@bottom-status-change="handleBottomChange" 
					:bottom-all-loaded="allLoaded"
					:bottom-distance="10"
					:auto-fill = "false"
					ref="loadmore">
					<div>
						<div v-for="(item,index) in list" @click.stop.prevent="queryOrderbyid(item)">
							<mt-cell :title="item.order_state | orderState"  :label="(item.apply_date + ' ' + item.apply_time) | time" >
								<div class="money">
									<div>{{item.goods_amount | formatMoney}} 元</div>
									<div class="mint-cell-label">{{item.out_stage_count}}/{{item.total_stage_count}}期</div>
								</div>
								<div class="handle">
									<i class="iconfont icon-qrcode" v-if="item.order_state=='01'" @click.stop.prevent="cardPopup(item)"></i>
									<mt-button type="primary" plain size="small" v-if="item.order_state=='02' || item.order_state=='03'"  @click.stop.prevent="recordList(item.serial_no)">扣款记录</mt-button>
								</div>
							</mt-cell>
						</div>
					</div>
					<div slot="top" class="mint-loadmore-top">
						<span class="f14">
							刷新数据...
						</span>
					</div>
					<div slot="bottom" class="mint-loadmore-bottom">
						<span class="f14">
							加载更多...
						</span>
					</div>
				</mt-loadmore>
			</div>
		</div>
		
		<!-- 信用卡号后六位弹框 -->
		<mt-popup v-if="cardPopupVisible" v-model="cardPopupVisible" :closeOnClickModal="false">  
            <Entercard6 v-on:confirm="recreateOrder" v-on:close="closePopup('cardPopupVisible')" title="请输入信用卡号后六位" />
        </mt-popup>

		<!-- 扣款记录弹框 -->
		<mt-popup v-model="recordPopupVisible" :closeOnClickModal="false" position="right" :modal="false">
            <div class="full">
                <Record v-on:close="closePopup('recordPopupVisible')" :data="recordDetail" />
            </div>
        </mt-popup>

		<!-- 交易详情弹框 -->
		<mt-popup v-model="orderPopupVisible" :closeOnClickModal="false" position="right" :modal="false">
            <div class="full">
				<QueryTrade v-on:close="closePopup('orderPopupVisible')" :data="orderDetail" />
            </div>
        </mt-popup>

    </div>
</template>

<script>
import Entercard6 from  '@/components/entercard6';
import Record from  '@/pages/custom/record';
import QueryTrade from  '@/pages/custom/querytrade';
import { InfiniteScroll , MessageBox , Toast} from 'mint-ui';
// 订单相关service
import * as LFQCreateOrderService from '@/services/LFQCreateOrder'

export default {
	components: {
       Entercard6,Record,QueryTrade
	},
	data() {
		return {
			cardPopupVisible : false,
			recordPopupVisible : false,
			orderPopupVisible : false,
			orderDetail : {}, // 订单信息
			recordDetail : {}, // 扣款记录
			tmpDataForQrCode : { // 存储重新生成二维码的参数
				serial_no : '',
				card_6th : '',
				credit_card_no : ''
			}, 
			fixed : false,
			list: [], // 分期查询列表
        	bottomStatus: false,
			topStatus: false,
			allLoaded: false,
			wrapperHeight: 0,
			start : 1
		}
	},
	methods: {
		handleTopChange(status) {
			this.moveTranslate = 1;
			this.topStatus = status;
		},
		handleBottomChange(status) {
			this.bottomStatus = status;
		},
		translateChange(translate) {
			const translateNum = +translate;
			this.translate = translateNum.toFixed(2);
			this.moveTranslate = (1 + translateNum / 70).toFixed(2);
		},
		// 刷新数据
		loadTop(){
			this.loading = true;
			var self = this;
			LFQCreateOrderService.queryOrder({
				servlet_type : 'query_order',
				open_id : self.GLOBAL.OPENID,
				prdt_no : self.GLOBAL.PRDTNO,
				min_count : '1',
				max_count : '20'
			}).then(function(res){
				if(res.error_response){
					return MessageBox({
						title: '提示',
						message: res.error_response.resp_msg,
						confirmButtonHighlight : true
					});
				}
				self.$refs.loadmore.onTopLoaded();
				self.start = 1;
				self.list = [];
				self.list = self.list.concat(res.list);
				self.loading = false;
				self.allLoaded = false;
			});
		},
		// 加载更多数据
		loadBottom(){
			this.loading = true;
			var self = this;
			LFQCreateOrderService.queryOrder({
				servlet_type : 'query_order',
				open_id : self.GLOBAL.OPENID,
				prdt_no : self.GLOBAL.PRDTNO,
				min_count : self.start,
				max_count : self.start+20
			}).then(function(res){
				if(res.error_response){
					return MessageBox({
						title: '提示',
						message: res.error_response.resp_msg,
						confirmButtonHighlight : true
					});
				}
	
				self.$refs.loadmore.onBottomLoaded();
				self.start += 20;
				self.list = self.list.concat(res.list);
				self.loading = false;

				// 如果不满20条了，则再加载
				if(res.list.length < 20){
					self.allLoaded = true;
					return Toast({
						message: '无更多数据',
						position: 'center',
						duration: 3000
					});
				}

			});
		},
		queryOrderbyid(args){
			var self = this;

			if(args.order_state == '02' || args.order_state == '03'){
				LFQCreateOrderService.queryOrderbyid({
					servlet_type : 'query_orderByOrderId',
					open_id : self.GLOBAL.OPENID,
					serial_no : args.serial_no
				}).then(function(res){
					if(res.error_response){
						return MessageBox({
							title: '提示',
							message: res.error_response.resp_msg,
							confirmButtonHighlight : true
						});
					}
					self.orderDetail = Object.assign(self.orderDetail,res.list[0]);
					self.$eventBus.$emit('updateOrderDetail'); // querytrade.vue
					self.orderPopupVisible = true;
					self.fixed = true;
				});
			}
		},
		// 输入后六位
		cardPopup(args){
			this.cardPopupVisible = true;
			this.fixed = true;
			this.tmpDataForQrCode.credit_card_no = args.credit_card_no;
			this.tmpDataForQrCode.serial_no = args.serial_no;
		},
		recreateOrder(value){
			var self = this;
			this.tmpDataForQrCode.card_6th = value;
			
			if(value != this.tmpDataForQrCode.credit_card_no.toString().slice(-6)){
				this.$eventBus.$emit('entercardReload')
				return Toast({
  					message: '信用卡号后六位不正确',
					position: 'center',
					duration: 3000
				});
			}

			LFQCreateOrderService.recreateOrder({
				servlet_type : 'recreate_order',
				serial_no : self.tmpDataForQrCode.serial_no,
				card_6th : value,
				lng:self.GLOBAL.LNG,
                lat:self.GLOBAL.LAT,
				credit_card_no : self.tmpDataForQrCode.credit_card_no
			}).then(function(res){
				if(res.error_response){
					return MessageBox({
						title: '提示',
						message: res.error_response.resp_msg,
						confirmButtonHighlight : true
					});
				}

				// 跳转至二维码页面
				sessionStorage.setItem('serial_no',self.tmpDataForQrCode.serial_no);  
				sessionStorage.setItem('credit_card_no',self.tmpDataForQrCode.credit_card_no);

				// 存储生成二维码的时间
				sessionStorage.setItem('creat_qr_time',res.res_timestamp);
				self.$router.push('/c_payment/' + res.serial_no);
			});
		},
		recordList(id){
			var self = this;
			LFQCreateOrderService.queryOrderbyid({
				servlet_type : 'query_orderByOrderId',
				open_id : self.GLOBAL.OPENID,
				serial_no : id
			}).then(function(res){
				if(res.error_response){
					return MessageBox({
						title: '提示',
						message: res.error_response.resp_msg,
						confirmButtonHighlight : true
					});
				}
				self.recordDetail.list = res.record_list;
				self.recordDetail.serialNo = res.serial_no;
				self.$eventBus.$emit('updateRecordDetail'); // record.vue
				self.recordPopupVisible = true;
				self.fixed = true;
			});
		},
		closePopup(el){
			this[el] = false;
			this.fixed = false;
		},
		// 解决父级窗口一起滚动的bug
        fixedPage(e){
            e.preventDefault();
        }
	},
	created(){
		this.loadBottom();	
	},
	mounted(){
		this.wrapperHeight = document.documentElement.clientHeight  - 20;
	}
}
</script>

<style lang="less" rel="stylesheet/less">
	.page-loadmore-wrapper{
		.mint-cell-wrapper{
			border-bottom: 0;
		}
	}
</style>



