<template>
    <div>
        <a class="mint-cell mint-field">
            <div class="mint-cell-left"></div>
            <div class="mint-cell-wrapper">
                <!-- <div class="mint-cell-title">
                    <span class="mint-cell-text"></span>
                </div> -->
                <div class="mint-cell-value city-input">
                    <!-- 用于遮罩  input readyonly 还是会弹一个原生的确定按钮出来，所以把点击事件加在div上 -->
                    <div class="city-mask" @click="showPopup"></div> 
                    <input 
                        placeholder="营业地址 省/直辖市" 
                        type="text" 
                        class="mint-field-core" 
                        :value="address" />
                </div>
            </div>
            <div class="mint-cell-right"></div>
        </a>
        <a class="mint-cell mint-field">
            <div class="mint-cell-left"></div>
            <div class="mint-cell-wrapper">
                <!-- <div class="mint-cell-title">
                    <span class="mint-cell-text"></span>
                </div> -->
                <div class="mint-cell-value">
                    <input 
                        placeholder="请输入详细地址" 
                        type="text" 
                        class="mint-field-core" 
                        :value="detail"
                        v-on:input="updateDetail($event.target.value)"  />
                </div>
            </div>
            <div class="mint-cell-right"></div>
        </a>          
        <mt-popup
            v-model="popupVisible" position="bottom" :closeOnClickModal="f" popup-transition="popup-fade">
            <div class="picker-city" @touchmove="fixedPage">
                <mt-picker :slots="addressSlots" @change="onAddressChange" :visible-item-count="7" ></mt-picker>
            </div>
            <div class="mint-msgbox-btns">
                <button class="mint-msgbox-btn mint-msgbox-cancel" @click="closePopup">取消</button>
                <button class="mint-msgbox-btn mint-msgbox-confirm" @click="confirm" >确定</button>
            </div>
        </mt-popup>
    </div>
</template>

<script>
import map from "./map.js";
export default {
  props: ["address", "detail"],
  data() {
    return {
      f: false,
      popupVisible: false,
      addressSlots: [
        {
          flex: 1,
          values: Object.keys(map),
          className: "slot1",
          textAlign: "center"
        },
        {
          divider: true,
          content: "-",
          className: "slot2"
        },
        {
          flex: 1,
          values: [],
          className: "slot3",
          textAlign: "center"
        }
      ],
      pickerArr: [],
      addressForUpdate: ""
    };
  },
  computed: {},
  methods: {
    confirm: function() {
      this.popupVisible = false;
      this.$eventBus.$emit("getAddress", this.addressForUpdate);
    },
    closePopup: function() {
      this.popupVisible = false;
    },
    showPopup: function() {
      this.popupVisible = true;
    },
    onAddressChange(picker, values) {
      picker.setSlotValues(1, map[values[0]]);
      this.addressForUpdate = values.join("");
    },
    updateDetail(value) {
      this.$eventBus.$emit("getDetail", value);
    },
    // 解决父级窗口一起滚动的bug
    fixedPage(e) {
      e.preventDefault();
    }
  },
  created: function() {}
};
</script>

<style lang="less" rel="stylesheet/less" scoped>
.city-input {
  .city-mask {
    position: absolute;
    height: 30px;
    width: 100%;
    opacity: 0;
  }
}
.picker-city {
  width: 100vw;
  height: 200px;
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}
</style>


