<template>
	<div class="wrap">
		<mt-header title="取现">
			<router-link to="/b_comhome" slot="left">
				<mt-button icon="back">返回</mt-button>
			</router-link>
		</mt-header>
		<div class="main">
			<mt-cell title="结算银行账户">
				<span>111111****1111</span>
			</mt-cell>
			<mt-cell title="账户余额(元)">
				<span>5,000.00</span>
			</mt-cell>
			<div class="cash-box mt-20">
				<div class="input-box border_b">
					<CurrencyInput v-model="cashNumber" placeholder="请输入取现金额(元)" :min="amtLowLimit" :max="amtUplimit" v-on:error="isShowTip=true;isApplyNum=false"
					    v-on:success="isShowTip=false;isApplyNum=true" />
					<div class="is-danger help-bar" v-if="isShowTip">输入金额必须在{{amtLowLimit}}-{{amtUplimit}}之间</div>
				</div>
				<p class="tr blue_1 f14">全部取现</p>
				<div class="button-box">
					<mt-button type="primary" size="large" v-bind:disabled="!checked ||!isApplyNum" @click="">确定</mt-button>
				</div>
				<p class="tc gray_2 f14">单日只能取现一次，预计到账时间
					<span class="blue_1">9-21</span>
				</p>
			</div>
		</div>
	</div>
</template>
<script>
	import CurrencyInput from "@/components/currency";
	export default {
		components: {
			CurrencyInput
		},
		data() {
			return {
				amtLowLimit: 100,
				amtUplimit: 5000,
				isShowTip: false
			}
		}
	};

</script>
<style lang="less" scoped>
	@import "../../../../../styles/variable.less";
	@import "../../../../../styles/base.less";
	.cash-box {
		padding: 10px;
		background-color: #fff;
		p {
			line-height: 38px;
		}
	}

</style>



