<template>
	<div class="wrap">
		<mt-header title="订单信息">
			<mt-button icon="back" slot="left" @click="close">返回</mt-button>
		</mt-header>
		<div class="main">
			<mt-cell title="订单编号" :value="details.serial_no"></mt-cell>
			<mt-cell title="订单状态" :value="details.order_state | orderState"></mt-cell>
			<mt-cell title="消费日期" :value="(details.apply_date + ' ' + details.apply_time) | time"></mt-cell>
			<mt-cell title="分期总额" :value="details.stage_amt | formatMoney"></mt-cell>
			<mt-cell title="商品金额" :value="details.goods_amount | formatMoney"></mt-cell>
			<mt-cell title="分期卡号" :value="details.credit_card_no"></mt-cell>
			<mt-cell title="消费商户" :value="details.t_mer_name"></mt-cell>
			<mt-cell title="分期期数" :value="details.total_stage_count"></mt-cell>
			<mt-cell title="已扣期数" :value="details.out_stage_count"></mt-cell>
			<div class="btn">
				<mt-button type="primary" size="large" @click="showContract">查看分期合同</mt-button>
			</div>
		</div>
		<mt-popup v-model="contractPopupVisible" :closeOnClickModal="false" position="right" :modal="false">
			<div class="full">
				<Contract v-on:close="contractPopupVisible=false" :data="contractData" />
			</div>
		</mt-popup>
	</div>
</template>

<script>
	import Contract from '@/components/contract/';
	export default {
		props: ['data'],
		components: {
			Contract
		},
		data() {
			return {
				contractPopupVisible: false,
				// 合同中要显示的字段
				contractData: {
					title: '',
					cust_name: '', //乙方
					id_no: '', //身份证
					hp_no: '', // 手机
					serial_no: '',
					t_mer_name: '', // 消费商户
					stage_amt: '', // 分期总额
					total_stage_count: '', // 分期期数,
					check: true,
					apply_date: '' // 签订时间
				},
				// 订单信息中的字段
				details: {
					serial_no: '', // 订单编号
					order_state: '', // 订单状态
					apply_date: '', // 日期
					apply_time: '', // 时间
					stage_amt: '', // 分期总额
					goods_amount: '', // 产品金额
					credit_card_no: '', // 分期卡号
					t_mer_name: '', // 消费商户
					total_stage_count: '', // 分期期数
					out_stage_count: '' // 已扣期数
				}
			}
		},
		methods: {
			close() {
				this.$emit('close')
			},
			showContract() {
				this.contractPopupVisible = true;
				this.contractData.url = '@/contract/contract_1.md';
				this.contractData.title = '个人消费分期支付合同';
	
	
			},
			// 解决父级窗口一起滚动的bug
			fixedPage(e) {
				e.preventDefault();
			}
		},
		created() {
			var self = this;
			this.$eventBus.$on('updateOrderDetail', function() {
				self.details = Object.assign(self.details, self.data);
				self.contractData = Object.assign(self.contractData, self.data);
			});
		}
	}
</script>

<style lang="less" rel="stylesheet/less" scoped>
	.btn {
		padding: 15px;
	}
</style>



