<template>
	<div class="wrap">
		<mt-header title="首页">
			<router-link to="/b_login" slot="right">
				<mt-button><i class="icon iconfont icon-log-out"></i> 退出</mt-button>
			</router-link>
		</mt-header>
		<div class="main">
			<div class="money-box">
				<p class="tc f16 gray_1">账户余额(元)</p>
				<p class="tc f40 line-70">5,000.00</p>
				<p class="tc f12 gray_2">请关注微信公众号【民生易分期】，进行账户操作</p>
			</div>
			<div class="button-box" v-if="!isSigned">
				<mt-button type="primary" size="large" @click="signed"><i class=" f20 icon iconfont icon-qiandao" v-if="!isSigning">&nbsp; </i>{{text}}</mt-button>
			</div>
			<div class="trans-box mt-15" v-if="isSigned">
				<div class="trans-cont flex flex-justify  pb_15 pt_15">
					<div class="trans-item flex flex-col border_r" @click="payPopupVisible = true">
						<i class="icon iconfont icon-xiaofeifenqi tc"></i>
						<p class="tc f16">分期消费</p>
					</div>
					<div class="trans-item flex flex-col" @click="merPopupVisible = true">
						<i class="icon iconfont icon-qrcode tc"></i>
						<p class="tc f16">二维码</p>
					</div>
				</div>
	
			</div>
		</div>
		<!-- 分期消费弹框 -->
		<mt-popup v-model="payPopupVisible" :closeOnClickModal="false" position="right" :modal="false">
			<div class="full">
				<Payment v-on:close="closePopup('payPopupVisible')" />
			</div>
		</mt-popup>
		<!-- 签到商户弹框 -->
		<mt-popup v-model="merPopupVisible" class="pop-box">
			<div class="flex flex-jcenter flex-acenter title border-b">
				<div>签到商户</div>
			</div>
	
			<!-- <mt-cell title="商户名称" v-for="item in 4">
	                    <span class="icon iconfont icon-qrcode f30" @click="merPopupVisible=false;codePopupVisible=true"></span>
	                </mt-cell> -->
	
		</mt-popup>
		<!-- 二维码弹框 -->
		<mt-popup v-model="codePopupVisible" class="pop-box">
			<div class="title flex flex-jend">
				<span class="icon iconfont icon-close f30 gray_2" @click="codePopupVisible=false"></span>
			</div>
			<div class="qrcode-box flex flex-jcenter flex-acenter">
				<QrCode :value="text" :size="200" />
			</div>
	
		</mt-popup>
		<!-- 填写信息弹框 -->
		<mt-popup v-model="infoPopupVisible" class="pop-box">
			<div class="flex flex-jcenter flex-acenter title border-b">
				<div>个人信息</div>
	
			</div>
			<div class="info-box">
	
				<div class="input-box border_b">
					<span class="f14 gray_1">姓名</span>
					<input type="text" disabled placeholder="田瑞丰">
				</div>
				<div class="input-box border_b">
					<span class="f14 gray_1" v-if="idFocus">身份证号码</span>
					<input v-model="id_no" name="idno" placeholder="身份证号码" type="tel" maxlength='18' />
				</div>
				<div class="input-box border_b">
					<span class="f14 gray_1" v-if="cardFocus">银行卡号</span>
					<input v-model="cardNo" type="text" placeholder="银行卡号">
				</div>
	
			</div>
			<div class="button-box">
				<mt-button type="primary" size="large">提交</mt-button>
				<p class="tip f14 gray_1">提示：请<span class="red_1">慎重填写</span>以上信息，活动奖励将发放至相应账户</p>
			</div>
	
		</mt-popup>
	
	</div>
</template>

<script>
	import Payment from "@/pages/business/compayment";
	import QrCode from '@/components/qrcode';
	// import Field from '@/components/field';
	export default {
		components: {
			Payment,
			QrCode
		},
		data() {
			return {
				text: "签到",
				isSigning: false,
				isSigned: true,
				payPopupVisible: false,
				merPopupVisible: false,
				codePopupVisible: false,
				infoPopupVisible: false,
				id_no: '',
				cardNo: '',
				idFocus: false,
				cardFocus: false
	
			};
		},
		methods: {
			signed() {
				this.isSigning = true;
				this.text = "正在匹配地理位置...";
				var _this = this;
				setTimeout(function() {
					_this.isSigned = true;
					_this.isSigning = false;
				}, 2000);
			},
			closePopup(el) {
				this[el] = false;
				this.fixed = false;
			},
			// 解决父级窗口一起滚动的bug
			fixedPage(e) {
				e.preventDefault();
			}
		}
	};
</script>

<style lang="less" scoped>
	@import "../../../../styles/variable.less";
	.money-box {
		background-color: #fff;
		padding: 20px 10px;
		.line-70 {
			line-height: 70px;
		}
	}
	
	.trans-box {
		padding: 0 10px;
		background-color: #fff;
		.icon {
			font-size: 40px;
			color: @blue_1;
		}
		button {
			height: 36px;
			width: 80px;
		}
		.pt_15 {
			padding-top: 15px;
		}
		.pb_15 {
			padding-bottom: 15px;
		}
		.trans-item {
			width: 50%;
			p {
				line-height: 36px;
			}
		}
	}
	
	.title {
		padding-right: 10px;
	}
	
	.info-box {
		padding: 0 10px;
		.input-box {
			padding-top: 10px;
		}
		span {
			padding-left: 14px;
		}
	}
	
	input[disabled] {
		background-color: #fff;
	}
	
	.tip {
		padding-top: 10px;
	}
	
	.red_1 {
		color: @red_1;
	}
</style>



