<template>
    <div>
        <mt-cell title="分期期数">
            <span @click="sheetVisible = true">
                <input type="text" readonly="readonly" v-model="stageNum">
                <i class="fa fa-caret-down pl-5"></i>
            </span>

        </mt-cell>
        <mt-popup v-model="sheetVisible" position="bottom" class="pop-box">
                <div class="flex flex-jcenter flex-acenter title border-b">
                    
                    <div>分期期数</div>
                    <div class="title-right">
                        <mt-button type="primary" size="small" @click="btnOK">确定</mt-button>
                    </div>
                </div>
                <mt-radio align="right" class="page-part" title="" v-model="stageSelect" :options="options">
                </mt-radio>
           

        </mt-popup>

    </div>
</template>
<script>
import * as LFQCreateOrderService from '@/services/LFQCreateOrder'
export default {
    props:["stage"],
    data() {
        return {
            sheetVisible: false,
            stageNum: this.stage,
            stageArray: [],
            data: [],
            currentData:{},
            stageSelect:this.stage
        }
    },

    methods: {
        stageView() {
            
            var _this = this;
            var currentData = {};
            LFQCreateOrderService.queryStages().then(function(res) {
                _this.data = res;
                _this.data.forEach(function(el,index) {
                    _this.stageArray.push(el.stage + '期');
                    if (el.stage == _this.stageNum.substring(0, _this.stageNum.length - 1)) {
                    _this.currentData = _this.data[index];
                }
                }, _this);
                 _this.$eventBus.$emit("getCurrentData", _this.currentData);
                
            });
            
        },
        btnOK (){
            this.sheetVisible=false;
            var _this = this;
            this.data.forEach(function(el, index) {
                if (_this.stageSelect.substring(0, _this.stageSelect.length - 1) == el.stage) {
                    _this.currentData = _this.data[index];
                }
            }, this);
            this.stageNum = this.stageSelect;
            this.$eventBus.$emit("getCurrentData", _this.currentData);
        }
    },
    watch: {
        // stageNum: function(newValue) {
        //     // console.log(newValue)
            
        // }
    },
    computed: {

    },
    created() {
        this.options = this.stageArray;
        
    },
    mounted() {
        this.$nextTick(() => {
            this.stageView();
            
        });
        console.log(this.stage)
    },
}
</script>
<style lang="less" scoped>

input{
    text-align: right;
}
.pr-10 {
    padding-right: 10px;
}
.pb-5{
    padding-bottom: 5px;
}
.pop-box {
   width: 100%;
   .title-right{
       top: -5px;
   }
}

</style>



