<template>
	<div class="wrap">
		<mt-header title="修改手机号码">
			<mt-button icon="back" slot="left" @click="close">返回</mt-button>
		</mt-header>
		<div class="main">
			<mt-cell title="原手机号码">
	
			</mt-cell>
			<mt-cell title="13420987654">
				<mt-button type="primary" plain>获取验证码</mt-button>
	
			</mt-cell>
			<div class="input-box">
				<input type="num" placeholder="验证码" maxlength="6">
			</div>
			<div class="button-box">
				<mt-button type="primary" size="large" v-bind:disabled="!isAllowed">下一步</mt-button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				isAllowed: false
			}
		},
		methods: {
			close() {
				this.$emit('close')
			},
			// 解决父级窗口一起滚动的bug
			fixedPage(e) {
				e.preventDefault();
			}
		}
	}
</script>

<style lang="less" scoped>
	// .input-box{
	//     input{
	//         background-color: #fff;
	//     }
	// }
</style>



