import Vue from 'vue'
import Router from 'vue-router'
// 个人
import Apply from '@/pages/custom/apply/'
import Home from '@/pages/custom/home/'
import Record from '@/pages/custom/record/'
import QueryTrade from '@/pages/custom/querytrade/'
import Detail from '@/pages/custom/detail/'
import NextApply from '@/pages/custom/nextapply'
import Payment from '@/pages/custom/payment'
import PayResult from '@/pages/custom/payresult'
import GetGeoError from '@/pages/custom/getgeoerror'


// 商户
import Login from '@/pages/business/login'
import Register from '@/pages/business/register'
import ComPayment from '@/pages/business/compayment'
import ComPayResult from '@/pages/business/payresult'
import ForgetPsw from '@/pages/business/forgetpsw'
import ComHome from '@/pages/business/commercial/comhome'
import ComCash from '@/pages/business/commercial/comcash'
import ComInquiry from '@/pages/business/commercial/cominquiry'
import ComTransList from '@/pages/business/commercial/comtranslist'
import ComUserSet from '@/pages/business/commercial/comuserset'
//营业员
import StaffHome from '@/pages/business/staffhome'

// 客户经理
import ManagerHome from '@/pages/business/manager/home'
import ManagerForm from '@/pages/business/manager/form'
import ManagerUpload from '@/pages/business/manager/upload'
import ManagerPhoto from '@/pages/business/manager/photo'
import ManagerExample from '@/pages/business/manager/example'
import ManagerRate from '@/pages/business/manager/rate'

//中间页面
import InstalInquiry from '@/pages/wxinitpages/instalinquiry'


// import InstalSearch from '@/pages/instalsearch'
Vue.use(Router)
const router = new Router({
	routes: [
		{ 
			path: '*', // 404 
			redirect: '/c_apply' 
		},
		{ 
			path: '/', 
			redirect: '/c_apply' 
		},
		{
			path: '/c_apply',
			name: 'Apply',
			component: Apply
		},
		{
			path: '/c_nextapply',
			name: 'NextApply',
			component: NextApply
		},
		{
			path: '/c_payment/:serialNo',
			name: 'Payment',
			component: Payment
		},
		{
			path: '/c_home',
			name: 'Home',
			component: Home
		},
		{
			path: '/c_record',
			name: 'Record',
			component: Record
		},
		{
			path: '/c_detail',
			name: 'Detail',
			component: Detail
		},
		{
			path: '/c_payresult',
			name: 'PayResult',
			component: PayResult,
			props: (route) => ({ 
				result: route.query.result,
				reason: route.query.reason
			})
		},
		{
			path: '/c_getgeoerror',
			name: 'GetGeoError',
			component: GetGeoError
		},
		{
			path: '/c_querytrade',
			name: 'QueryTrade',
			component: QueryTrade
		},
		// 商户
		{
			path: '/b_login',
			name: 'Login',
			component: Login
		},
		{
			path: '/b_register',
			name: 'Register',
			component: Register
		},
		{
			path: '/b_payresult',
			name: 'ComPayResult',
			component: ComPayResult
		},
		{
			path: '/b_forgetpsw',
			name: 'ForgetPsw',
			component: ForgetPsw
		},
		{
			path: '/b_comhome',
			name: 'ComHome',
			component: ComHome
		},
		{
			path: '/b_comcash',
			name: 'ComCash',
			component: ComCash
		},
		{
			path: '/b_cominquiry',
			name: 'ComInquiry',
			component: ComInquiry
		},
		{
			path: '/b_comtranslist',
			name: 'ComTransList',
			component: ComTransList
		},
		{
			path: '/b_compayment',
			name: 'ComPayment',
			component: ComPayment
		},
		{
			path: '/b_comuserset',
			name: 'ComUserSet',
			component: ComUserSet
		},
		//营业员
		{
			path: '/b_staffhome',
			name: 'StaffHome',
			component: StaffHome
		},


		//中间页
		{
			path: '/instalinquiry',
			name: 'InstalInquiry',
			component: InstalInquiry	
		},
		// 客户经理
		{
			path: '/b_managerhome',
			name: 'ManagerHome',
			component: ManagerHome	
		},
		{
			path: '/b_managerform',
			name: 'ManagerForm',
			component: ManagerForm	
		},
		{
			path: '/b_managerupload',
			name: 'ManagerUpload',
			component: ManagerUpload	
		},
		{
			path: '/b_managerphoto',
			name: 'ManagerPhoto',
			component: ManagerPhoto	
		},
		{
			path: '/b_managerexample',
			name: 'ManagerExample',
			component: ManagerExample	
		},
		{
			path: '/b_managerrate',
			name: 'ManagerRate',
			component: ManagerRate	
		}
	]
})

export default router


